<template>

    <div class="dropdown" v-click-outside="close" :class="disabled ? 'disabled' : null">
        <div class="label" v-on:click="!disabled ? is_open = true : null">
            <div class="icon" v-if="value && value.icon"><i :class="value.icon"></i></div>
            <div class="text" v-if="placeholder && value == null">{{ placeholder }}</div>
            <div class="text" v-if="value">
                {{ value.text }}
            </div>
            <div class="caret">
                <i class="fa fa-caret-down"></i>
            </div>
        </div>
        <div class="items" :class="is_open ? 'open' : ''">
            
            <div class="search" v-if="is_search">
                <i class="icon fa" :class="search_value.length > 0 ? 'fa-times clear' : 'fa-search'" v-on:click="search_value = ''"></i>
                <input v-model="search_value" class="search_field" type="text" :placeholder="search_placeholder"/>
            </div>
            <scroll-view class="list">
                <div class="item" v-for="item in list" :key="item.val" v-on:click="set(item.value)">
                    <div class="icon" v-if="item.icon"><i :class="item.icon"></i></div>
                    {{ item.text }}
                </div>
            </scroll-view>

        </div>
    </div>

</template>

<script>

export default {

    model: {
        prop: 'val',
        event: 'change'
    },

    props: ['val', 'items', 'disabled', 'placeholder', 'is_search', 'search_placeholder'],

    created: function (){

        this.local_value = this.val;

    },

    data: function (){

        return {


            local_value: null,
            is_open: false,
            search_value: "",

        }

    },

    computed: {

        value: function (){

            if(this.local_value == null){
                return null;
            }
            
            var index = this.items.map(function (i){ return i.value; }).indexOf(this.local_value);

            if(index == -1){
                return null;
            }

            return this.items[index];
        },

        list: function (){

            if(!this.items){
                return [];
            }

            if(this.search_value.trim().length == 0){
                return this.items;
            }

            var filtred_list = [];

            var item;
            var search_string = this.search_value.toLowerCase();
            for(var i = 0; i < this.items.length; i++){
                
                item = this.items[i];

                if(item.text.toLowerCase().indexOf(search_string) != -1 || (typeof item.val === 'string' && item.val.toLowerCase().indexOf(search_string) != -1)){
                    filtred_list.push(item);
                }

            }

            return filtred_list;
        }

    },

    watch: {

        val: function (){

            this.local_value = this.val;
            this.$emit('change', this.val);

        }

    },

    methods: {

        set: function (val){

            this.close();

            this.local_value = val;
            this.$emit('change', this.local_value);

        },

        close: function (){
            this.is_open = false;
            this.search_value = "";
        }

    }

}

</script>

<style scoped>

.dropdown {
    display: flex;
    color: #000000;
    font-size: 12px;
    position: relative;
}

.dropdown .label {
    display: flex;
    flex: 1;
    flex-direction: row;
    padding: 7px;
    border: 1px solid #DCDCDC;
    border-radius: 3px;
    padding: 10px;
    background-color: #ffffff;
}

.dropdown .label:hover {
    cursor: pointer;
    border-color: #5b96cd;
}

.dropdown.disabled .label {
    cursor: default;
    border-color: #DCDCDC !important;
}

.dropdown .label .icon {
    display: flex;
    width: 14px;
    height: 12px;
    text-align: center;
    margin-right: 7px;
}

.dropdown .label .icon i {
    line-height: 14px;
}

.dropdown .label .text {
    margin-right: auto;
}


.dropdown .label .caret {
    margin-left: 10px;
}

.dropdown.disabled .label .caret {
    color: #808080;
}

.dropdown .label .caret i {
    height: 12px;
    line-height: 14px;
}

.dropdown .items {
    display: flex;
    flex-direction: column;
    min-width: 100%;
    position: absolute;
    border: 1px solid #DCDCDC;
    margin-top: 35px;
    border-radius: 3px;
    background-color: #ffffff;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s linear 0.12s, opacity 0.12s linear, margin-top 0.12s linear;
    box-shadow: 0px 2px 4px #00000033;
    overflow: hidden;
    z-index: 9999;
}

.dropdown .items.open {
    visibility: visible;
    opacity: 1;
    margin-top: 43px;
    transition-delay: 0s;
}

.dropdown .items .list {
    display: flex;
    flex-direction: column;
    max-height: 200px;
    overflow-y: auto;
}


.dropdown .items .item {
    display: flex;
    padding: 10px;
    flex-direction: row;
    cursor: pointer;
}

.dropdown .items .item .icon {
    width: 14px;
    height: 12px;
    text-align: center;
    margin-right: 7px;
}


.dropdown .items .item .icon i {
    font-size: 12px;
    line-height: 14px;
}

.dropdown .items .item:hover {
    color: #ffffff;
    background-color: #5b96cd;
}

.dropdown .items .search {
    display: flex;
    flex-direction: row;
    padding: 10px;
}

.dropdown .items .search .icon {
    position: absolute;
    font-size: 13px;
    margin-top: 12px;
    margin-left: 10px;
    color: #808080;
}

.dropdown .items .search .icon.clear {
    cursor: pointer;
    margin-left: 11px;
}

.dropdown .items .search .search_field {
    flex: 1;
    padding-left: 32px;
    font-size: 12px;
}


</style>