<template>

    <div class="param-editor">

        <div class="field">
            <div class="title">
                {{ $ml.get('param_name') }}
            </div>
            <input class="input" v-focus type="text" v-model="name" :placeholder="$ml.get('enter_param_name') + '...'" />
        </div>

        <div class="field">
            <div class="title">
                {{ $ml.get('type_param') }}
            </div>

            <Dropdown :items="types" v-model="type" :placeholder="$ml.get('choose_type') + `...`"/>
        </div>

        <div class="field">
            <div class="title">
                {{ $ml.get('param_description') }}
            </div>
            <div class="descriptions">
                <div class="description" v-for="(description, index) in descriptions" :key="description.lang">
                    <div class="subtitle">
                        <Dropdown v-model="description.lang" :items="languages" :placeholder="`${$ml.get('choose_language')}...`"/>
                        <div class="func">
                            <i class="icon fa fa-times" @click="removeDescription(index)"></i>
                        </div>
                    </div>
                    <Editor :height="'200px'" :options="markdown_options" mode="markdown" v-model="description.text"/>
                </div>
                <button class="cancel add_button" @click="addDescription"><i class="icon fa fa-plus"></i>{{ $ml.get('add_description') }}</button>
            </div>
        </div>


        <div class="field">
            <Checkbox v-model="optional">{{ this.$ml.get('is_optional_param') }}</Checkbox>
        </div>

        <div class="field">
            <div class="title">
                {{ $ml.get('conditions') }}
            </div>
            <div class="conditions">
                <div class="list">
                    <draggable v-model="conditions" group="conditions">                    
                        <div class="condition" v-for="(condition, index) in conditions" :key="condition.type + index">
                        
                            <Dropdown :items="conditions_types" v-model="condition.type" :placeholder="$ml.get('choose_condition_type') + `...`"  class="types" @change="updateValueCondition(index)"/>
                            <input class="input" v-if="['min_limit', 'max_limit', 'min_length', 'max_length'].indexOf(condition.type) > -1" type="number" min="0" v-model.number="condition.value" :placeholder="$ml.get('enter_value') + `...`"/>
                            <input class="input" v-if="condition.type == 'regex'" type="text" v-model="condition.value" :placeholder="$ml.get('enter_value') + `...`"/>
                            <div class="value_list"  v-if="condition.type == 'value_list'">
                                <div class="item" v-for="(item, value_index) in condition.value" :key="item">
                                    <div class="data">
                                        <input v-if="type == 'int' || type == 'float'" type="number" class="editable_data" v-model.lazy.number="condition.value[value_index]" :placeholder="`${$ml.get('enter_value')}...`"/>
                                        <input  v-if="type == 'string'" type="text" class="editable_data" v-model.lazy="condition.value[value_index]" :placeholder="`${$ml.get('enter_value')}...`" />
                                    </div>
                                    <div class="func">
                                        <i class="fa fa-times" @click="removeConditionValue(index, value_index)"></i>
                                    </div>
                                </div>
                                <input class="value" v-if="type == 'int' || type == 'float'" type="number" :placeholder="$ml.get('enter_value') + `...`" v-on:keydown.enter="(event) => { addConditionValue(index, event.target.value); event.target.value = ''; }"/>
                                <input class="value" v-if="type == 'string'" type="text" :placeholder="$ml.get('enter_value') + `...`" v-on:keydown.enter="(event) => { addConditionValue(index, event.target.value); event.target.value = ''; }"/>
                            </div>

                            <a class="remove_btn" @click="removeCondition(index)">{{ $ml.get('remove_action') }}</a>
                        </div>
                    </draggable>
                </div>
                <button class="add_button cancel" @click="addCondition"><i class="icon fa fa-plus"></i>{{ $ml.get('add_condition') }}</button>
            </div>
        </div>
    </div>

</template>

<style lang="scss" scoped>

.param-editor {
    
    display: flex;
    flex-direction: column;

    .field {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0px;
        }

        .title {
            font-size: 14px;
            margin-bottom: 10px;
            font-weight: 500;
        }

        .subtitle {
            display: flex;
            flex-direction: row;
            align-items: center;
            font-size: 13px;
            margin-bottom: 10px;

            .func {
                margin-left: auto;

                .icon {
                    color: #808080;
                    font-size: 14px;
                    cursor: pointer;

                    &:hover {
                        color: #5b96cd;
                    }

                }
            }

        }

        .input {
            font-size: 13px;
        }

        .descriptions {
            display: flex;
            flex-direction: column;
            width: auto;

            .description {
                display: flex;
                flex-direction: column;
                width: auto;
                margin-bottom: 10px;
            }

            .add_button {
                width: auto;

                .icon {
                    margin-right: 7px;
                }
            }
        }

        .textarea {
            width: auto;
            padding: 15px;
            border-radius: 3px;
            font-size: 13px;
        }

        .checkbox {
            font-size: 14px;
            margin-top: 5px;
            margin-bottom: 15px;
        }

        .conditions {
            display: flex;
            flex-direction: column;

            .condition {
                display: flex;
                flex-direction: column;
                margin-bottom: 10px;

                .types {
                    margin-bottom: 10px;
                }

                .remove_btn {
                    color: #808080;
                    font-size: 13px;
                    margin: 0 auto;
                    margin-top: 10px;
                }

                .value_list {
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                    max-width: 360px;

                    .item {
                        display: flex;
                        flex-direction: row;
                        background-color: #ededed;
                        border-radius: 3px;
                        width: calc(50% - 4px);
                        align-items: center;
                        margin-bottom: 7px;
                        margin-right: 7px;

                        &:nth-child(2n){
                            margin-left: 1px;
                            margin-right: 0px;
                        }

                        .editable_data {
                            background: none;
                            border: none;
                            padding: 7px;
                            min-width: 20px;
                            width: calc(100% - 25px);

                            &:focus {
                                background-color: #ffffff;
                                border: 1px solid #5b96cd;
                                padding: 6px;

                            }
                        }

                        .func {
                            margin: 0px 10px;
                            i {
                                color: #808080;
                                cursor: pointer;

                                &:hover {
                                    color: #5b96cd;
                                }
                            }
                        }
                    
                    }

                    .value {
                        border-radius: 3px;
                        min-width: 20px;
                        width: calc(50% - 25px);
                        margin-bottom: 7px;
                    }
                }
                
            }

            .add_button {

                margin-top: 10px;

                .icon {
                    margin-right: 7px;
                }
            }
        }

    }

}

</style>


<script>


import Vue from 'vue'

import draggable from 'vuedraggable'
import { isNumber } from 'util';
import 'tui-editor/dist/tui-editor.css';
import 'tui-editor/dist/tui-editor-contents.css';
import 'codemirror/lib/codemirror.css';
import { Editor } from '@toast-ui/vue-editor'

export default {

    props: ['param_object'],

    components: {
        draggable,
        Editor
    },

    created: function (){

        if(!this.$props.param_object){
            return;
        }

        this.name = this.$props.param_object.name;
        this.descriptions = JSON.parse(JSON.stringify(this.$props.param_object.descriptions));
        this.type = this.$props.param_object.type;
        this.optional = this.$props.param_object.optional;
        this.conditions = JSON.parse(JSON.stringify(this.$props.param_object.conditions));

    },

    data: function (){

        return {

            name: "",
            descriptions: [],
            type: 'int',
            optional: false,
            conditions: [],

            types: [
                {
                    text: "Int",
                    value: "int"
                },
                {
                    text: "Float",
                    value: "float",
                },
                {
                    text: "String",
                    value: "string",
                }
            ],

            conditions_types: [
                {
                    text: this.$ml.get('min_limit'),
                    value: 'min_limit',
                },
                {
                    text: this.$ml.get('max_limit'),
                    value: 'max_limit',
                },
                {
                    text: this.$ml.get('min_length'),
                    value: 'min_length',
                },
                {
                    text: this.$ml.get('max_length'),
                    value: 'max_length',
                },
                {
                    text: this.$ml.get('value_list'),
                    value: 'value_list'
                },
                {
                    text: this.$ml.get('regular_expression'),
                    value: 'regex'
                }
            ],

            languages: [
                {
                    text: this.$ml.get('ru'),
                    value: 'ru'
                },
                {
                    text: this.$ml.get('en'),
                    value: 'en'
                }
            ],

            markdown_options: {
                useCommandShortcut: true,
                useDefaultHTMLSanitizer: true,
                usageStatistics: true,
                hideModeSwitch: true,
                toolbarItems: [
                    'heading',
                    'bold',
                    'italic',
                    'strike',
                    'ul',
                    'ol'
                ]
            }

        }

    },

    methods: {

        addCondition: function (){

            this.conditions.push({
                type: 'min_limit',
                value: 0
            });

        },

        addConditionValue: function (index, value){


            this.conditions[index].value.push(value);

        },

        updateValueCondition: function (index){

            var condition = this.conditions[index];
            
            switch(condition.type){
                case "min_limit":
                case "max_limit":
                case "min_length":
                case "max_length":
                    if(isNumber(condition.value)){
                        return;
                    }

                    condition.value = 0;
                    break;
                case "value_list":
                    if(Array.isArray(condition.value)){
                        return;
                    }

                    condition.value = [];

                    break;
                case "regex":
                    if(typeof condition.value === "string"){
                        return;
                    }

                    condition.value = "/[0-9]+/g"
                    break;
            }

        },

        removeConditionValue: function (condition_index, value_index){

            Vue.delete(this.conditions[condition_index].value, value_index);
        },

        removeCondition: function (index){

            Vue.delete(this.conditions, index);
        },

        addDescription: function (){

            this.descriptions.push({
                lang: 'en',
                text: ''
            });
            
        },

        removeDescription: function (index){
            
            Vue.delete(this.descriptions, index);

        }


    }
}

</script>