<template>

    <div class="scroll_view" ref="scroll_view">

        <div class="scroll_view_wrapper" ref="scroll_wrapper" @scroll="scrollHandler">
            
            <div class="content" ref="content">
                <slot></slot>
            </div>

        </div>

        <div class="scrollbar" ref="scroll_bar" @mousedown="scrollStartMove" v-show="showScrollBar">

            <div class="thumb" ref="scroll_thumb"></div>

        </div>


    </div>

</template>

<script>

export default {

    data: function (){

        return {
            showScrollBar: false
        }

    },


    mounted: function (){

        var scrollViewWidth = this.$refs.scroll_view.offsetWidth;
        var contentWidth = this.$refs.content.offsetWidth;

        var scrollBarWidth = scrollViewWidth - contentWidth;
        
        this.$refs.content.style.width = `calc(100% + ${scrollBarWidth}px)`;

    },

    updated: function (){
       
        this.updateScrollbar();

    },

    methods: {

        scrollPosition: function (){

            return { left: this.$refs.scroll_wrapper.scrollLeft, top: this.$refs.scroll_wrapper.scrollTop }

        },

        updateScrollbar: function (){

            var scrollTop = this.$refs.scroll_wrapper.scrollTop;

            var scrollHeight = this.$refs.scroll_wrapper.scrollHeight;
            var scrollBarHeight = this.$refs.scroll_bar.clientHeight;
            var scrollViewHeight = this.$refs.scroll_view.clientHeight


            this.showScrollBar = scrollHeight > scrollViewHeight;

            var delta = scrollHeight / scrollBarHeight;

            var thumbTopOffset = scrollTop / delta;
            var thumbHeight = scrollBarHeight / delta;

            this.$refs.scroll_thumb.style = `margin-top: ${ thumbTopOffset }px; height: ${thumbHeight}`;

        },

        scrollHandler: function (){

            this.updateScrollbar();            

        },

        scrollStartMove: function (e){

            document.body.addEventListener('mousemove', this.scrollMoveThumb);
            document.body.addEventListener('mouseup', this.scrollStopMove);

            this.scrollMoveThumb(e);

        },

        scrollStopMove: function (){

            document.body.removeEventListener('mouseup', this.scrollStopMove);
            document.body.removeEventListener('mousemove', this.scrollMoveThumb);

        },

        scrollMoveThumb: function (e){

            e.preventDefault();


            var delta = this.$refs.scroll_wrapper.scrollHeight / this.$refs.scroll_wrapper.clientHeight;

            var scrollBarPositionY = this.$refs.scroll_bar.getBoundingClientRect().y;

            var currentPosition = e.clientY;

            var scrollPosition = (currentPosition - scrollBarPositionY) * delta;

            this.scrollTo(scrollPosition);

        },



        scrollTo: function (y){

            this.$refs.scroll_wrapper.scrollTop = y;
        },

        animateScrollTo: function (y, ms){

            var currentScroll = this.scrollPosition().top;

            window.console.log({ currentScroll, y });

            if(currentScroll == y){
                return;
            }
            
            var diff = y - currentScroll;

            var step = diff / ms;

            var animateValue = currentScroll;
            var self = this;

            var interval = setInterval(function (){

                if(animateValue >= y && currentScroll < y || animateValue <= y && currentScroll > y){
                    clearInterval(interval);
                    self.scrollTo(y);
                    return;
                }

                animateValue += step;

                window.console.log(animateValue);

                self.scrollTo(animateValue);

            }, 1);

        }

    }
}

</script>

<style scoped>

.scroll_view {
    display: flex;
    flex: 1;
    position: relative;
    overflow: hidden !important;
}

.scroll_view .scroll_view_wrapper {
    width: 100%;
    height: 100%;
    padding-right: 100px;
    overflow-y: scroll;
    overflow-x: hidden;
}

.scroll_view .scrollbar {
    cursor: pointer;
    visibility: hidden;
    opacity: 0;
    border-radius: 10px;
    position: absolute;
    height: calc(100% - 10px);
    top: 0;
    left: 100%;
    width: 7px;
    margin-top: 5px;
    margin-left: -9px;
    transition: visibility linear 0.1s, opacity linear 0.1s;
}

.scroll_view .scroll_view_wrapper .content {
    width: 100%;
}

.scroll_view:hover .scrollbar {
    visibility: visible;
    opacity: 1;
}

.scroll_view .scrollbar:hover {
    background-color: #00000015;
}

.scroll_view .scrollbar .thumb {
    width: 5px;
    min-height: 10px;
    cursor: pointer;
    background-color: #00000030;
    border-radius: 10px;
    transition: width linear 0.15s;
}

.scroll_view .scrollbar .thumb:hover {
    background-color: #00000050;
}


.scroll_view .scrollbar:hover .thumb {
    width: 100%;
}


</style>