<template>

    <div class="popups" v-show="$popups.stack.length > 0">

        <div class="background_click" @click="$popups.closeAll">

        </div>

        <div class="popup" v-for="(popup, index) in $popups.stack" v-show="index == $popups.stack.length - 1" :key="popup.title" >

            <div class="panel" v-if="popup.title">
                <div class="title" v-if="popup.title">
                    {{ popup.title }}
                </div>
                <div class="controls">
                    <div class="control" @click="$popups.close">
                        <i class="fa fa-times"></i>
                    </div>
                </div>
            </div>

            <div class="content" v-if="popup.text">
                {{ popup.text }}
            </div>

            <div class="content" v-if="popup.html" v-html="popup.html"></div>

            <div class="content component" v-if="popup.component">
                <component :is="popup.component" ref="component" v-bind="popup.data"></component>
            </div>

            <div class="buttons" v-if="popup.buttons && popup.buttons.length > 0">
                <input type="button" v-for="button in popup.buttons" :key="button.text" class="button" :class="button.type" :value="button.text" @click="buttonHandler(button)" />
            </div>

        </div>

    </div>

</template>

<script>

export default {

    updated: function (){

        if(this.$refs.component && this.$refs.component.length > 0){
            this.$popups.setCurrentComponent(this.$refs.component[this.$refs.component.length - 1]);
        }

    },

    methods: {

        buttonHandler: function (button){

            if(button.callback){
                button.callback();
            }

            if(button.type == 'success' || button.type == 'warning' || button.type == 'cancel'){
                this.$popups.close();
            }

        }

    }

}

</script>

<style lang="scss" scoped>

.popups {

    position: fixed;
    display: flex;
    flex-direction: column;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    background-color: #00000090;

    .background_click {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    .popup {

        display: flex;
        position: relative;
        min-width: 400px;
        margin: 100px auto;
        box-shadow: 0px 5px 7px #00000040;
        background-color: #ffffff;
        flex-direction: column;
        border-radius: 3px;

        .panel {
            display: flex;
            flex-direction: row;
            border-bottom: 1px solid #DCDCDC;

            .title {
                padding: 20px;
                font-size: 15px;
                font-weight: 500;
            }

            .controls {

                display: flex;
                flex-direction: row;
                margin-left: auto;
                align-items: center;
                margin-right: 15px;
                
                .control {
                    padding: 5px;
                    width: 17px;
                    height: 17px;
                    text-align: center;
                    font-size: 17px;
                    line-height: 14px;
                    border-radius: 17px;
                    color: #808080;
                    cursor: pointer;

                    &:hover{
                        background-color: #ededed;
                        color: #000000;
                    }


                }

            }

        }

        .content {
            padding: 20px;
            font-size: 15px;

            &.component {
                display: flex;
                flex-direction: column;
            }
        }

        .buttons {
            display: flex;
            flex-direction: row;
            padding: 0px 20px 20px 20px;

            .button {
                margin-right: 15px;
                font-size: 13px;
                font-weight: 500;
            }
        }
    }


}

</style>