<template>

    <div class="field-editor">

        <div class="field">
            <div class="title">
            {{ $ml.get('field_name') }}
            </div>

            <input class="input" v-focus type="text" v-model="name" :placeholder="`${$ml.get('enter_field_name')}...`" />
        </div>

        <div class="field">
            <div class="title">
            {{ $ml.get('field_type') }}
            </div>

            <Dropdown :items="types" v-model="type" :placeholder="`${$ml.get('choose_type_field')}...`" />

        </div>

        <div class="field">
            <div class="title">
                {{ $ml.get('description') }}
            </div>
            <div class="descriptions">
                <div class="description" v-for="(description, index) in descriptions" :key="description.lang">
                    <div class="subtitle">
                        <Dropdown v-model="description.lang" :items="languages" :placeholder="`${$ml.get('choose_language')}...`"/>
                        <div class="func">
                            <i class="icon fa fa-times" @click="removeDescription(index)"></i>
                        </div>
                    </div>
                    <Editor :height="'200px'" :options="markdown_options" mode="markdown" v-model="description.text"/>
                </div>
                <button class="cancel add_button" @click="addDescription"><i class="icon fa fa-plus"></i>{{ $ml.get('add_description') }}</button>
            </div>
        </div>
        <div class="field">

            <Checkbox v-model="is_array">{{ $ml.get('is_field_array') }}</Checkbox>
            <Checkbox v-model="optional">{{ $ml.get('is_optional_field') }}</Checkbox>

        </div>

    </div>

</template>

<script>

import Vue from 'vue'
import 'tui-editor/dist/tui-editor.css';
import 'tui-editor/dist/tui-editor-contents.css';
import 'codemirror/lib/codemirror.css';
import { Editor } from '@toast-ui/vue-editor'

export default {

    props: ['field_object', 'user_types'],
    components: {
        Editor
    },

    created: function (){

        if(this.field_object){

            this.name = this.field_object.name;
            this.descriptions = JSON.parse(JSON.stringify(this.field_object.descriptions));
            this.type = this.field_object.type;
            this.is_array = this.field_object.is_array;
            this.optional = this.field_object.optional;

        }

        

    },
    
    data: function (){

        return {
            name: '',
            type: 'int',
            descriptions: [],
            is_array: false,
            optional: false,

            custom_types: [],

            languages: [
                {
                    text: this.$ml.get('ru'),
                    value: 'ru'
                },
                {
                    text: this.$ml.get('en'),
                    value: 'en'
                }
            ],

            markdown_options: {
                useCommandShortcut: true,
                useDefaultHTMLSanitizer: true,
                usageStatistics: true,
                hideModeSwitch: true,
                toolbarItems: [
                    'heading',
                    'bold',
                    'italic',
                    'strike',
                    'ul',
                    'ol'
                ]
            }
            
        }

    },

    computed: {

        types: function (){

            var list = [
                {
                    text: "Int",
                    value: "int"
                },
                {
                    text: "Float",
                    value: "float"
                },
                {
                    text: "String",
                    value: "string"
                },
                {
                    text: "Boolean",
                    value: "boolean"
                },
                {
                    text: "Object",
                    value: "object"
                }
            ];

            if(this.user_types){
                this.user_types.map(function (type){

                    list.push({
                        text: type.name,
                        value: type.name
                    });

                });
            }

            return list;

        }

    },

    methods: {
        addDescription: function (){

            this.descriptions.push({
                lang: 'en',
                text: ''
            });
            
        },

        removeDescription: function (index){
            
            Vue.delete(this.descriptions, index);

        }

    }


}
</script>


<style lang="scss" scoped>

.field-editor {
    
    display: flex;
    flex-direction: column;

    .field {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0px;
        }

        .title {
            font-size: 14px;
            margin-bottom: 10px;
            font-weight: 500;
        }

        .input {
            font-size: 13px;
        }

        .subtitle {
            display: flex;
            flex-direction: row;
            align-items: center;
            font-size: 13px;
            margin-bottom: 10px;

            .func {
                margin-left: auto;

                .icon {
                    color: #808080;
                    font-size: 14px;
                    cursor: pointer;

                    &:hover {
                        color: #5b96cd;
                    }

                }
            }

        }

       
        .descriptions {
            display: flex;
            flex-direction: column;
            width: auto;

            .description {
                display: flex;
                flex-direction: column;
                width: auto;
                margin-bottom: 10px;
            }

            .add_button {
                width: auto;

                .icon {
                    margin-right: 7px;
                }
            }
        }

        .textarea {
            width: auto;
            padding: 15px;
            border-radius: 3px;
            font-size: 13px;
        }

        .checkbox {
            font-size: 14px;
            margin-top: 5px;
            margin-bottom: 15px;
        }

        .conditions {
            display: flex;
            flex-direction: column;

            .condition {
                display: flex;
                flex-direction: column;
                margin-bottom: 10px;

                .types {
                    margin-bottom: 10px;
                }

                .remove_btn {
                    color: #808080;
                    font-size: 13px;
                    margin: 0 auto;
                    margin-top: 10px;
                }
                
            }

            .add_button {

                margin-top: 10px;

                .icon {
                    margin-right: 7px;
                }
            }
        }

    }

}

</style>