<template>

    <div class="create-method-template">

        <div class="field">
            <div class="title">{{ $ml.get('method_name') }}</div>
            <input v-model="name" v-focus type="text" class="input" :placeholder="$ml.get('enter_method_name') + `...`"/>
        </div>

        <div class="field">
            <div class="title">{{ $ml.get('choose_template') }}</div>
            <Dropdown :items="list" v-model="template" class="input" :placeholder="$ml.get('choose_template') + `...`"/>
        </div>

    </div>

</template>

<script>

export default {

    props: ['templates'],

    created: function (){

        if(this.$props.templates){

            this.list = JSON.parse(JSON.stringify(this.$props.templates));
        }

    },

    data: function (){
        return {
            name: "",
            template: -1,
            list: []
        }
    }

}

</script>

<style lang="scss" scoped>

.create-method-template {
    display: flex;
    flex-direction: column;

    .field {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0px;
        }

        .title {
            font-size: 14px;
            margin-bottom: 10px;
            font-weight: 500;
        }

        .input {
            font-size: 13px;
        }

        .textarea {
            width: auto;
            padding: 15px;
            border-radius: 3px;
            font-size: 13px;
        }
    }

}

</style>
