<template>

    <div class="radio_group">
        
        <div class="radio_btn" v-for="item in items" :key="item.value" @click="set(item.value)" :class="val == item.value ? 'choose' : '' ">

            <div class="icon">
                <i :class="val == item.value ? 'far fa-dot-circle' : 'far fa-circle'"></i>
            </div>

            <div class="text">
                {{ item.text }}
            </div>

        </div>

    </div>


</template>

<script>

export default {
    
    model: {
        prop: 'val',
        event: 'change'
    },

    props: ['val', 'items'],

    created: function (){

        this.local_value = this.val;

    },

    computed: {

        value: function (){

            if(!this.local_value){
                return null;
            }
            
            var index = this.items.map(function (i){ return i.value; }).indexOf(this.local_value);

            if(index == -1){
                return null;
            }

            return this.items[index];
        }

    },

    watch: {

        val: function (){

            this.local_value = this.val;
            this.$emit('change', this.val);

        }

    },

    data: function (){
        return {

            local_value: null

        }    
    },


    methods: {
        
        set: function (val){

            this.local_value = val;
            this.$emit('change', this.local_value);

        },        

    }


}

</script>

<style scoped>

.radio_group {
    display: flex;
    flex-direction: column;
    font-size: 12px;
}

.radio_group .radio_btn {
    display: flex;
    flex-direction: row;
    cursor: pointer;
    margin-bottom: 10px;
}

.radio_group .radio_btn:last-child {
    margin-bottom: 0px;
}


.radio_group .radio_btn .icon {
    width: 12px;
    height: 12px;
    margin-right: 10px;
    color: #808080;
}

.radio_group .radio_btn.choose .icon,
.radio_group .radio_btn:hover .icon {
    color: #5b96cd;
}



</style>