export default {

    install: function (Vue, store){

        Vue.prototype.$schema = new Vue({
            

            created: function (){

                this.$watch('project', this.updateSchemaHandler, { deep: true });
                this.$watch('classes', this.updateSchemaHandler, { deep: true });
                this.$watch('types', this.updateSchemaHandler, { deep: true });
                this.$watch('errors', this.updateSchemaHandler, { deep: true });
                this.$watch('templates', this.updateSchemaHandler, { deep: true });

                window.console.log(store);

            },

            data: function (){

                return {

                    project: {
                        version: 1,
                        vars: {
                            error_counter: 0
                        }
                    },

                    classes: [],

                    types: [],

                    errors: [],

                    templates: {
                        methods: []
                    },

                    
                }

            },

            methods: {

                get: function (){

                    return {
                        project: this.project,
                        classes: this.classes,
                        types: this.types,
                        errors: this.errors,
                        templates: this.templates
                    }

                },

                addProject: function (project){

                    store.dispatch('addProject', project);

                },

                updateSchemaHandler: function (){

                    store.dispatch('updateProject', {
                        project: this.project,
                        classes: this.classes,
                        types: this.types,
                        errors: this.errors,
                        templates: this.templates
                    });
                
                },

                loadSchema: function (schema){

                    this.project = schema.project;
                    this.classes = schema.classes;
                    this.types = schema.types;
                    this.errors = schema.errors;
                    this.templates = schema.templates;

                },

                loadSchemaById: function (id){

                    var schema = store.state.projects.find((element) => {
           
                        return (element.project.id == parseInt(id));
            
                    });

                    window.console.log(schema);

                    this.loadSchema(schema);
            
                },

                addClass: function (name, descriptions){

                    this.classes.push({

                        name: name,
                        descriptions: descriptions,
                        methods: []

                    });

                },


                /*
                ##################################
                    WORK WITH CLASS STRUCTURE
                ##################################
                */

                /*
                ========================================
                    Methods for create structure
                ========================================
                */

                addMethod: function (class_index, name, descriptions, is_admin, is_auth, is_db, errors){

                    this.classes[class_index].methods.push({

                        name: name,
                        descriptions: descriptions,
                        is_db: is_db,
                        is_auth: is_auth,
                        is_admin: is_admin,
                        params: [],
                        response: { fields: [] },
                        errors: errors
                        
                    });

                },

                addMethodByTemplate: function (class_index, name, template_index){

                    var template_object = JSON.parse(JSON.stringify(this.templates.methods[template_index].template));

                    template_object.name = name;

                    this.classes[class_index].methods.push(template_object);
                            

                },

                addParam: function (class_index, method_index, name, descriptions, type, optional, conditions){

                    this.classes[class_index].methods[method_index].params.push({

                        name: name,
                        descriptions: descriptions,
                        type: type,
                        optional: optional,
                        conditions: conditions
                        
                    });

                },

                /*
                ========================================
                    Methods for edit structure
                ========================================
                */


                editClass: function (class_index, name, descriptions){

                    this.classes[class_index].name = name;
                    this.classes[class_index].descriptions = descriptions;

                },

                editMethod: function (class_index, method_index, name, descriptions, is_admin, is_auth, is_db, errors){
                    this.classes[class_index].methods[method_index].name = name;
                    this.classes[class_index].methods[method_index].descriptions = descriptions;
                    this.classes[class_index].methods[method_index].is_admin = is_admin;
                    this.classes[class_index].methods[method_index].is_auth = is_auth;
                    this.classes[class_index].methods[method_index].is_db = is_db;
                    this.classes[class_index].methods[method_index].errors = errors;
                },

                editParam: function (class_index, method_index, param_index, name, descriptions, type, optional, conditions){

                    this.classes[class_index].methods[method_index].params[param_index].name = name;
                    this.classes[class_index].methods[method_index].params[param_index].descriptions = descriptions;
                    this.classes[class_index].methods[method_index].params[param_index].type = type;
                    this.classes[class_index].methods[method_index].params[param_index].optional = optional;
                    this.classes[class_index].methods[method_index].params[param_index].conditions = conditions;

                },

                /*
                ==============================================
                    Methods for remove elements structure
                ==============================================
                */

                removeClass: function (class_index){

                    Vue.delete(this.classes, class_index);

                },

                removeMethod: function (class_index, method_index){

                    Vue.delete(this.classes[class_index].methods, method_index);

                },

                removeParam: function (class_index, method_index, param_index){

                    Vue.delete(this.classes[class_index].methods[method_index].params, param_index);

                },

                /*
                ##################################
                        WORK WITH TYPES
                ##################################
                */

                addType: function (name){

                    if(["boolean", "int", "float", "string"].indexOf(name.toLowerCase()) != -1){
                        return;
                    }

                    var index = this.types.map(function (type){  
                        
                        return type.name.toLowerCase();

                    }).indexOf(name.toLowerCase());

                    if(index != -1){
                        return;
                    }

                    this.types.push({

                        name: name,
                        fields: []

                    });
                },

                editType: function (type_index, name){

                    var old_name = this.types[type_index].name;
                    this.types[type_index].name = name;

                    this.updateAllTypes(old_name, name);

                },

                removeType: function (type_index){
                    var type = this.types[type_index];

                    var old_name = type.name;

                    this.updateAllTypes(old_name, "string");

                    Vue.delete(this.types, type_index);
                },

                /*
                ======================================
                            TYPE FIELDS
                ======================================
                */

                addTypeField: function (type_index, back_stack, name, type, descriptions, is_array, optional){

                    var local_fields = this.types[type_index];

                    for(var i = 0; i < back_stack.length; i++){

                        local_fields = local_fields.fields[back_stack[i]];

                    }

                    var new_field = {
                        name: name,
                        type: type,
                        descriptions: descriptions,
                        is_array: is_array,
                        optional: optional
                    };

                    if(type == 'object'){
                        new_field.fields = [];
                    }

                    local_fields.fields.push(new_field);
                },

                editTypeField: function (type_index, back_stack, field_index, name, type, descriptions, is_array, optional){
                    var local_fields = this.types[type_index];

                    for(var i = 0; i < back_stack.length; i++){

                        local_fields = local_fields.fields[back_stack[i]];

                        window.console.log(local_fields);

                    }

                    local_fields.fields[field_index].name = name;
                    local_fields.fields[field_index].type = type;
                    local_fields.fields[field_index].descriptions = descriptions;
                    local_fields.fields[field_index].is_array = is_array;
                    local_fields.fields[field_index].optional = optional;

                    if(type == 'object'){
                        if(!local_fields.fields[field_index].fields){
                            local_fields.fields[field_index].fields = [];
                        }
                    } else {
                        delete local_fields.fields[field_index].fields;
                    }

                },

                removeTypeField: function (type_index, back_stack, field_index){

                    var local_fields = this.types[type_index];

                    for(var i = 0; i < back_stack.length; i++){

                        local_fields = local_fields.fields[back_stack[i]];

                    }

                    Vue.delete(local_fields.fields, field_index);

                },

                updateAllTypes: function (old_name, name){

                    // Check types
                    
                    for(var i = 0; i < this.types.length; i++){
                        this.types[i].fields = this.updateFields(this.types[i].fields, old_name, name);
                    }
                    
                    for(i = 0; i < this.classes.length; i++){
        
                        for(var j = 0; j < this.classes[i].methods.length; j++){
        
                            this.classes[i].methods[j].response.fields = this.updateFields(this.classes[i].methods[j].response.fields, old_name, name);
        
                        }
        
                    }
        
                },
        
                updateFields: function (fields, old_name, name){
        
                    for(var i = 0; i < fields.length; i++){
        
                        if(fields[i].type == old_name){
                            window.console.log(`* Update ${fields[i].name}`);
                            fields[i].type = name;
        
                        }
        
                        if(fields[i].type == 'object'){
                            fields[i] = this.updateFields(fields[i]);
                        }
        
                    }
        
                    return fields;
        
                },

                /*
                ##################################
                        WORK WITH RESPONSES
                ##################################
                */
                
                /*
                ======================================
                            RESPONSE FIELDS
                ======================================
                */

                addResponseField: function (class_index, method_index, back_stack, name, type, descriptions, is_array, optional){

                    var local_fields = this.classes[class_index].methods[method_index].response;

                    for(var i = 0; i < back_stack.length; i++){

                        local_fields = local_fields.fields[back_stack[i]];

                        window.console.log(local_fields);

                    }

                    var new_field = {
                        name: name,
                        type: type,
                        descriptions: descriptions,
                        is_array: is_array,
                        optional: optional
                    };

                    if(type == 'object'){
                        new_field.fields = [];
                    }

                    local_fields.fields.push(new_field);
                },

                editResponseField: function (class_index, method_index, back_stack, field_index, name, type, descriptions, is_array, optional){

                    var local_fields = this.classes[class_index].methods[method_index].response;

                    for(var i = 0; i < back_stack.length; i++){

                        local_fields = local_fields.fields[this.back_stack[i]];

                        window.console.log(local_fields);

                    }

                    local_fields.fields[field_index].name = name;
                    local_fields.fields[field_index].type = type;
                    local_fields.fields[field_index].descriptions = descriptions;
                    local_fields.fields[field_index].is_array = is_array;
                    local_fields.fields[field_index].optional = optional;

                    if(type == 'object'){
                        if(!local_fields.fields[field_index].fields){
                            local_fields.fields[field_index].fields = [];
                        }
                    } else {
                        delete local_fields.fields[field_index].fields;
                    }


                },

                removeResponseField: function (class_index, method_index, back_stack, field_index){

                    var local_fields = this.classes[class_index].methods[method_index].response;

                    for(var i = 0; i < back_stack.length; i++){

                        local_fields = local_fields.fields[back_stack[i]];

                    }

                    Vue.delete(local_fields.fields, field_index);

                },

                /*
                ======================================
                            BASE CLASS
                ======================================
                */

                addBaseClass: function (class_index, method_index, back_stack, type){
                    var local_fields = this.classes[class_index].methods[method_index].response;

                    for(var i = 0; i < back_stack.length; i++){

                        local_fields = local_fields.fields[back_stack[i]];


                    }

                    Vue.set(local_fields, 'base', type);
                },

                editBaseClass: function (class_index, method_index, back_stack, type){

                    var local_fields = this.classes[class_index].methods[method_index].response;

                    for(var i = 0; i < back_stack.length; i++){

                        local_fields = local_fields.fields[back_stack[i]];


                    }

                    Vue.set(local_fields, 'base', type);
                },

                removeBaseClass: function (class_index, method_index, back_stack){

                    var local_fields = this.classes[class_index].methods[method_index].response;

                    for(var i = 0; i < back_stack.length; i++){

                        local_fields = local_fields.fields[back_stack[i]];

                    }

                    Vue.delete(local_fields, 'base');

                },

                /*
                ##################################
                        WORK WITH ERRORS
                ##################################
                */

                addError: function (title, description){

                    var new_error = {
                        code: this.project.vars.error_counter + 1,
                        title: title,
                        description: description
                    };

                    this.errors.push(new_error);
                    this.project.vars.error_counter++;

                },

                removeError: function (error_index){

                    var error_object = this.errors[error_index];

                    Vue.delete(this.errors, error_index);

                    this.removeErrorFromAllMethods(error_object.code);

                },
                
                removeErrorFromAllMethods: function (error_code){
            
                    for(var i = 0; i < this.classes.length; i++){

                        for(var j = 0; j < this.classes[i].methods.length; j++){

                            var index = this.classes[i].methods[j].errors.indexOf(error_code);

                            if(index > -1){
                                Vue.delete(this.classes[i].methods[j].errors, index);
                            }

                        }

                    }
                }

            }

        });
        
    }

}