<template>

    <div class="checkbox" :class="state ? 'checked' : ''" v-on:click="toggle">
        <div class="icon">
            <i class="fa fa-check-square" v-if="state"></i>
            <i class="far fa-square" v-if="!state"></i>
        </div>
        <div class="title">
            <slot></slot>
        </div>
    </div>

</template>

<script>

export default {
    model: {
        prop: 'checked',
        event: 'change'
    },

    props: ['checked'],

    created: function (){

        this.state = this.checked;

    },

    data: function (){

        return {
            state: false
        }

    },

    watch: {
        checked: function (){

            this.state = this.checked;
            this.$emit('change', this.checked);

        }
    },

    methods: {

        toggle: function (){

            this.state = !this.state;
            this.$emit('change', this.state);

        }

    }

}

</script>

<style scoped>

.checkbox {
    display: flex;
    flex-direction: row;
    cursor: pointer;
}

.checkbox .icon {
    display: flex;
    align-items: center;
    color: #808080;
    margin-right: 7px;
}

.checkbox.checked .icon, .checkbox:hover .icon {
    color: #5b96cd;
}


</style>