<template>

    <div class="schema" v-if="$schema">

        <!-- CLASSES -->
        <div class="column">

            <div class="title">
                <div class="text">{{ $ml.get('classes') }}</div>
                <div class="controls">
                    <div class="control" @click="addClass">
                        <i class="icon fa fa-plus"></i>
                    </div>
                </div>
            </div>

            <div class="list">
                <draggable v-model="$schema.classes" group="classes" :options="{ ghostClass: 'ghost' }">
                    <div class="item" :class="index == current_class ? 'choose' : '' " v-for="(class_object, index) in $schema.classes" :key="class_object.name" @click="openClass(index)">

                        <div class="name">    
                            {{class_object.name}}
                        </div>

                        <div class="controls">

                            <div class="control">
                                <i class="icon fa fa-cog" @click.stop="editClass(index)"></i>
                            </div>

                            <div class="control">
                                <i class="icon fa fa-times" @click.stop="removeClass(index)"></i>
                            </div>
                        </div>
                        
                    </div>
                </draggable>
            </div>

        </div>

        <!-- METHODS -->
        <div class="column">
            
            <div class="title">
                <div class="text">{{ $ml.get('methods') }}</div>
                <div class="controls">
                    <div class="control" @click="addMethod(current_class)">
                        <i class="icon fa fa-plus"></i>
                    </div>
                    <div class="control" @click="addMethodByTemplate(current_class)">
                        <i class="icon fa fa-cubes"></i>
                    </div>
                </div>
            </div>

            <div class="list" v-if="current_class != null">
                <draggable v-model="$schema.classes[current_class].methods" group="methods">
                    <div class="item" :class="index == current_method ? 'choose' : '' " v-for="(method_object, index) in $schema.classes[current_class].methods" :key="method_object.name" @click="openMethod(current_class, index)">

                        <div class="name">    
                            {{method_object.name}}
                        </div>

                        <div class="info">
                            <i class="icon fa fa-lock" v-if="method_object.is_auth"></i>
                            <i class="icon fa fa-shield-alt" v-if="method_object.is_admin"></i>
                            <i class="icon fa fa-database" v-if="method_object.is_db"></i>
                        </div>

                        <div class="controls">

                            <div class="control">
                                <i class="icon fa fa-cog"  @click.stop="editMethod(current_class, index)"></i>
                            </div>

                            <div class="control">
                                <i class="icon fa fa-times"  @click.stop="removeMethod(current_class, index)"></i>
                            </div>
                        </div>
                        
                    </div>
                </draggable>
            </div>



        </div>

        <!-- PARAMS -->
        <div class="column">

            <div class="title">

                <div class="text">{{ $ml.get('params') }}</div>
                <div class="controls">
                    <div class="control" @click="addParam(current_class, current_method)">
                        <i class="icon fa fa-plus"></i>
                    </div>
                </div>
                
            </div>

            <div class="list" v-if="current_class != null && current_method != null">
                <draggable v-model="$schema.classes[current_class].methods[current_method].params" group="params">
                    <div class="item" v-for="(param_object, index) in $schema.classes[current_class].methods[current_method].params" :key="param_object.name" @click.stop="editParam(current_class, current_method, index)">

                        <div class="name">    
                            {{param_object.name}}
                        </div>

                        <div class="info">
                            <span class="icon">{{ param_object.type }}</span>
                            <i v-if="param_object.optional" class="icon far fa-question-circle"></i>
                        </div>

                        <div class="controls">

                            <div class="control">
                                <i class="icon fa fa-times" @click.stop="removeParam(current_class, current_method, index)"></i>
                            </div>

                        </div>
                        
                    </div>
                </draggable>
            </div>

        </div>
    </div>

</template>

<script>

import Vue from 'vue'
import draggable from 'vuedraggable'

import ClassEditorPopup from '@/components/popups/ClassEditorPopup'
import MethodEditorPopup from '@/components/popups/MethodEditorPopup'
import ParamEditorPopup from '@/components/popups/ParamEditorPopup'
import ChooseErrorPopup from '@/components/popups/ChooseErrorPopup'
import CreateFromTemplatePopup from '@/components/popups/CreateFromTemplatePopup'


export default {

    props: ['project'],

    components: {
        draggable
    },

    created: function (){
        
        Vue.component('ClassEditorPopup', ClassEditorPopup);
        Vue.component('MethodEditorPopup', MethodEditorPopup);
        Vue.component('ParamEditorPopup', ParamEditorPopup);
        Vue.component('ChooseErrorPopup', ChooseErrorPopup);
        Vue.component('CreateFromTemplatePopup', CreateFromTemplatePopup);

    },

    data: function (){

        return {

            schema: null,

            current_class: null,
            current_method: null,

        }

    },

    
    methods: {

        updateStoreSchema: function (){
            this.$store.dispatch('updateProject', this.schema);
        },

        openClass: function (class_index){

            if(class_index == this.current_class) return;

            this.current_method = null;
            this.current_class = class_index;
        },

        openMethod: function (class_index, method_index){

            if(class_index == this.current_class && method_index == this.current_method) return;

            this.current_class = class_index;
            this.current_method = method_index;
        },

        
        /*
        =========================================
        * Methods for create data
        =========================================
        */

        addClass: function (){

            this.$popups.open({

                title: this.$ml.get('create_class'),
                component: "ClassEditorPopup",
                
                        
                buttons: [
                    {
                        text: this.$ml.get('create_action'),
                        type: "success",
                        callback: () => {

                            var name = this.$popups.getComponent().name;
                            var descriptions = this.$popups.getComponent().descriptions;
                            
                            this.$schema.addClass(name, descriptions);

                        }
                    },
                    {
                        text: this.$ml.get('cancel'),
                        type: "cancel"
                    }
                ]

            });

        },

        addMethod: function (class_index){

            if(!this.$schema.classes[class_index]){
                return;
            }

            this.$popups.open({

                title: this.$ml.get('create_method'),
                component: "MethodEditorPopup",

                buttons: [
                    {
                        text: this.$ml.get('create_action'),
                        type: "success",
                        callback: () => {

                            var name = this.$popups.getComponent().name;
                            var descriptions = this.$popups.getComponent().descriptions;
                            var is_db = this.$popups.getComponent().is_db;
                            var is_auth = this.$popups.getComponent().is_auth;
                            var is_admin = this.$popups.getComponent().is_admin;
                            var errors = this.$popups.getComponent().errors;
                            
                            this.$schema.addMethod(class_index, name, descriptions, is_admin, is_auth, is_db, errors);
                            

                        }
                    },
                    {
                        text: this.$ml.get('cancel'),
                        type: "cancel"
                    }
                ]

            });

        },

        addMethodByTemplate: function (class_index){

            if(!this.$schema.classes[class_index]){
                return;
            }

            this.$popups.open({

                title: this.$ml.get('create_method'),
                component: "CreateFromTemplatePopup",
                data: {
                    templates: this.$schema.templates.methods.map(function (template, index){
                        return {
                            text: template.name,
                            value: index
                        }
                    })
                },
                buttons: [
                    {
                        text: this.$ml.get('create_action'),
                        type: "success",
                        callback: () => {

                            var name = this.$popups.getComponent().name;
                            var template = this.$popups.getComponent().template;
                            
                            if(template == -1){
                                return;
                            }

                            this.$schema.addMethodByTemplate(class_index, name, template);
                        }
                    },
                    {
                        text: this.$ml.get('cancel'),
                        type: "cancel"
                    }
                ]

            });

        },

        addParam: function (class_index, method_index){
            
            if(!this.$schema.classes[class_index] || !this.$schema.classes[class_index].methods[method_index] ){
                return;
            }

            
            this.$popups.open({

                title: this.$ml.get('create_param'),
                component: "ParamEditorPopup",

                buttons: [
                    {
                        text: this.$ml.get('create_action'),
                        type: "success",
                        callback: () => {

                            var name = this.$popups.getComponent().name;
                            var descriptions = this.$popups.getComponent().descriptions;
                            var type = this.$popups.getComponent().type;
                            var optional = this.$popups.getComponent().optional;
                            var conditions = this.$popups.getComponent().conditions;
                           
                            
                            this.$schema.addParam(class_index, method_index, name, descriptions, type, optional, conditions);
                            

                        }
                    },
                    {
                        text: this.$ml.get('cancel'),
                        type: "cancel"
                    }
                ]

            });

        },
        
        /*
        =========================================
        * Methods for edit data
        =========================================
        */
        
        editClass: function (class_index){
            
            if(!this.$schema.classes[class_index]){
                return;
            }

            var class_object = this.$schema.classes[class_index];

            this.$popups.open({

                title: this.$ml.get('edit_class'),
                component: "ClassEditorPopup",

                data: {
                    class_object: class_object
                },
                        
                buttons: [
                    {
                        text: this.$ml.get('save_action'),
                        type: "success",
                        callback: () => {

                            var name = this.$popups.getComponent().name;
                            var descriptions = this.$popups.getComponent().descriptions;
                            
                            this.$schema.editClass(class_index, name, descriptions);

                        }
                    },
                    {
                        text: this.$ml.get('cancel'),
                        type: "cancel"
                    }
                ]

            });


        },

        editMethod: function (class_index, method_index){

            if(!this.$schema.classes[class_index] || !this.$schema.classes[class_index].methods[method_index]){
                return;
            }

            var method_object = this.$schema.classes[class_index].methods[method_index];

            this.$popups.open({

                title: this.$ml.get('edit_method'),
                component: "MethodEditorPopup",
                data: {
                    method_object: method_object
                },

                buttons: [
                    {
                        text: this.$ml.get('save_action'),
                        type: "success",
                        callback: () => {

                            var name = this.$popups.getComponent().name;
                            var descriptions = this.$popups.getComponent().descriptions;
                            var is_db = this.$popups.getComponent().is_db;
                            var is_auth = this.$popups.getComponent().is_auth;
                            var is_admin = this.$popups.getComponent().is_admin;
                            var errors = this.$popups.getComponent().errors;
                            
                            this.$schema.editMethod(class_index, method_index, name, descriptions, is_admin, is_auth, is_db, errors);

                        }
                    },
                    {
                        text: this.$ml.get('cancel'),
                        type: "cancel"
                    }
                ]

            });

        },

        editParam: function (class_index, method_index, param_index){

            if(!this.$schema.classes[class_index] || !this.$schema.classes[class_index].methods[method_index] || !this.$schema.classes[class_index].methods[method_index].params[param_index]){
                return;
            }

            var param_object = this.$schema.classes[class_index].methods[method_index].params[param_index];

            this.$popups.open({

                title: this.$ml.get('edit_param'),
                component: "ParamEditorPopup",

                data: {
                    param_object: param_object,
                },

                buttons: [
                    {
                        text: this.$ml.get('save_action'),
                        type: "success",
                        callback: () => {

                            var name = this.$popups.getComponent().name;
                            var descriptions = this.$popups.getComponent().descriptions;
                            var type = this.$popups.getComponent().type;
                            var optional = this.$popups.getComponent().optional;
                            var conditions = this.$popups.getComponent().conditions;
                                                       
                            this.$schema.editParam(class_index, method_index, param_index, name, descriptions, type, optional, conditions);
                            

                        }
                    },
                    {
                        text: this.$ml.get('cancel'),
                        type: "cancel"
                    }
                ]

            });

        },

        /*
        =========================================
        * Methods for remove data
        =========================================
        */

        removeClass: function (class_index){

            if(!this.$schema.classes[class_index]){
                return;
            }

            this.$popups.open({

                title: this.$ml.get('remove_class'),
                html: this.$ml.with('class_name', this.$schema.classes[class_index].name).get('want_remove_class'),
                buttons: [
                    {
                        text: this.$ml.get('remove_action'),
                        type: "warning",
                        callback: () => {

                            if(this.current_class == class_index){
                                this.current_class = null;
                                this.current_method = null;
                            }

                            this.$schema.removeClass(class_index);

                        }
                    },
                    {
                        text: this.$ml.get('cancel'),
                        type: "cancel"
                    }
                ]

            });

           

        },

        removeMethod: function (class_index, method_index){

            if(!this.$schema.classes[class_index] || !this.$schema.classes[class_index].methods[method_index] ){
                return;
            }


            this.$popups.open({

                title: this.$ml.get('remove_method'),
                html: this.$ml.with('method_name', this.$schema.classes[class_index].methods[method_index].name).get('want_remove_method'),
                buttons: [
                    {
                        text: this.$ml.get('remove_action'),
                        type: "warning",
                        callback: () => {

                             if(this.current_class == class_index && this.current_method == method_index){
                                this.current_method = null;
                            }

                            this.$schema.removeMethod(class_index, method_index);

                        }
                    },
                    {
                        text: this.$ml.get('cancel'),
                        type: "cancel"
                    }
                ]

            });
           

        },

        removeParam: function (class_index, method_index, param_index){

            if(!this.$schema.classes[class_index] || !this.$schema.classes[class_index].methods[method_index]  || !this.$schema.classes[class_index].methods[method_index].params[param_index] ){
                return;
            }

            this.$popups.open({

                title: this.$ml.get('remove_param'),
                html: this.$ml.with('param_name', this.$schema.classes[class_index].methods[method_index].params[param_index].name).get('want_remove_param'),
                buttons: [
                    {
                        text: this.$ml.get('remove_action'),
                        type: "warning",
                        callback: () => {


                            this.$schema.removeParam(class_index, method_index, param_index);

                        }
                    },
                    {
                        text: this.$ml.get('cancel'),
                        type: "cancel"
                    }
                ]

            });

            

        },

        

    }

}

</script>

<style lang="scss" scoped>

.schema {
    display: flex;
    flex-direction: row;
    flex: 1 1 auto;

    @import '@/styles/columns.scss';

}

</style>