<template>

    <div class="template-editor">

        <div class="field">
            <div class="title">
                {{ $ml.get('template_name') }}
            </div>

            <input type="text" class="input" v-model="name" :placeholder="$ml.get('enter_name') + `...`"/>

        </div>

        <div class="field">
            <div class="title">
                {{ $ml.get('choose_class') }}
            </div>

            <Dropdown :items="classList" v-model="current_class" :placeholder="$ml.get('choose_class') + `...`"/>

        </div>

        <div class="field" v-if="current_class > -1"> 
            <div class="title">
                {{ $ml.get('choose_method') }}
            </div>

            <Dropdown :items="methodList" v-model="current_method" :placeholder="$ml.get('choose_method') + `...`"/>

        </div>

    </div>

</template>

<script>

export default {

    data: function (){
        return {
            name: "",
            current_class: -1,
            current_method: -1
        }
    },

    computed: {

        classList: function (){

            return this.$schema.classes.map(function (class_object, index){

                return {
                    text: class_object.name,
                    value: index
                }

            });

        },

        methodList: function (){

            return this.$schema.classes[this.current_class].methods.map(function (method_object, index){

                return {
                    text: method_object.name,
                    value: index
                }

            });

        }
    }

}

</script>

<style lang="scss" scoped>

.template-editor {
    
    display: flex;
    flex-direction: column;

    .field {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0px;
        }

        .title {
            font-size: 14px;
            margin-bottom: 10px;
            font-weight: 500;
        }

        .input {
            font-size: 13px;
        }

        .textarea {
            width: auto;
            padding: 15px;
            border-radius: 3px;
            font-size: 13px;
        }

        .checkbox {
            font-size: 14px;
            margin-top: 5px;
            margin-bottom: 15px;
        }

    }
}
</style>
