<template>

    <div class="template-list">

        <div class="list" v-if="schema.templates.methods.length > 0">
            <div class="item" v-for="(template, index) in schema.templates.methods" :key="template.name">
                
                <span class="number">#{{ index + 1 }}</span><span class="name">{{ template.name }}</span><i class="icon fa fa-times" @click="removeTemplate(index)"></i>

            </div>
        </div>

        <button class="add_btn cancel" @click="addTemplate"><i class="icon fa fa-plus"></i>{{ $ml.get('add_template') }}</button>

    </div>

</template>

<style lang="scss" scoped>

.template-list {
    display: flex;
    flex-direction: column;

    .list {
        display: flex;
        flex-direction: column;
        border: 1px solid #ededed;
        border-radius: 5px;
        margin-bottom: 10px;

        .item {

            display: flex;
            flex-direction: row;
            align-items: center;
            padding: 15px;
            border-bottom: 1px solid #ededed;
            font-weight: 500;
            font-size: 14px;

            &:last-child {
                border-bottom: none;
            }

            .number {
                color: #808080;
                margin-right: 7px;
            }

            .icon {
                margin-left: auto;
                cursor: pointer;
                color: #808080;

                &:hover {
                    color: #5b96cd;
                }
            }
        }

        
    }

    .add_btn {
        flex: 1;
        .icon {
                margin-right: 7px;
            }
        }
}

</style>



<script>

import Vue from 'vue'

export default {

    created: function (){
        
        this.schema = this.$store.state.projects.find((element) => {

            return (element.project.id == parseInt(this.$store.state.current_project));

        });

        this.$watch('schema', this.updateStoreSchema, { deep: true });

    },

    data: function(){

        return {
            schema: null
        }
    },

    methods: {

        updateStoreSchema: function (){
            this.$store.dispatch('updateProject', this.schema);
        },

        addTemplate: function (){
            
            this.$popups.open({

                title: this.$ml.get('add_template'),
                component: "TemplateEditorPopup",

                buttons: [
                    {
                        text: this.$ml.get('create_action'),
                        type: "success",
                        callback: () => {

                            var name = this.$popups.getComponent().name;
                            var class_index = this.$popups.getComponent().current_class;
                            var method_index = this.$popups.getComponent().current_method;
                           
                            var template = JSON.parse(JSON.stringify(this.schema.classes[class_index].methods[method_index]));
                            
                            this.schema.templates.methods.push({
                                name: name,
                                template: template
                            });
                            

                        }
                    },
                    {
                        text: this.$ml.get('cancel'),
                        type: "cancel"
                    }
                ]

            });
        },

        removeTemplate: function (index){

            Vue.delete(this.schema.templates.methods, index);
        }
    }

}
</script>