<template>

    <div class="errors">

        <!-- Errors list -->
        <div class="column">
            
            <div class="title">
                <div class="text">{{ $ml.get('errors') }}</div>
                <div class="controls">
                    <div class="control" @click="addError">
                        <i class="icon fa fa-plus"></i>
                    </div>
                </div>
            </div>

            <div class="list">

                <draggable v-model="$schema.errors" group="errors">
                    <div class="item" v-for="(error, index) in $schema.errors" :class="index == current_error ? 'choose' : ''" :key="error.name" @click.stop="openError(index)">

                        <div class="name">#{{ error.code }} {{ error.title }}</div>
                        <div class="controls">

                            <div class="control">
                                <i class="icon fa fa-times"  @click.stop="removeError(index)"></i>
                            </div>
                        </div>

                    </div>
                </draggable>

            </div>

        </div>

        <!-- Edit form -->
        <div class="column" v-if="current_error != null">
            
            <div class="title">
                <div class="text">{{ $ml.get('edit_error') }}</div>
            </div>

            <div class="form">
                
                <div class="field">
                    <div class="title">{{ $ml.get('error_code') }}</div>
                    <input class="input" type="number" :placeholder="`${$ml.get('enter_error_code')}...`" v-model.number="$schema.errors[current_error].code"/>
                </div>
                <div class="field">
                    <div class="title">{{ $ml.get('error_name') }}</div>
                    <input class="input" type="text" :placeholder="`${$ml.get('enter_error_name')}...`" v-model="$schema.errors[current_error].title"/>
                </div>
                <div class="field">
                    <div class="title">{{ $ml.get('description') }}</div>
                    <textarea class="textarea" :placeholder="`${$ml.get('enter_description')}...`" v-model="$schema.errors[current_error].description"></textarea>
                </div>

            </div>

        </div>

        <!-- Methods list -->
        <div class="column" v-if="current_error != null">
            
            <div class="title">

                <div class="text">{{ $ml.get('crossing_methods') }}</div>

            </div>

            <div class="list">

                <div class="item" v-for="(item, index) in getMethodsWithError(current_error)" :class="index == current_error ? 'choose' : ''" :key="`${item.class_name}.${item.method_name}`">

                    <div class="name">{{ item.class_name }}.{{ item.method_name }}</div>
                    <div class="controls">

                        <div class="control">
                            <i class="icon fa fa-times"  @click.stop="removeErrorFromMethod(item.class_name, item.method_name, current_error)"></i>
                        </div>
                    </div>

                </div>

            </div>

        </div>
    </div>

</template>

<style lang="scss" scoped>

.errors {

    display: flex;
    flex-direction: row;
    flex: 1;
    min-height: 100%;

    @import '@/styles/columns.scss';
   
}

</style>


<script>

import Vue from 'vue'
import draggable from 'vuedraggable'

import ErrorEditorPopup from "@/components/popups/ErrorEditorPopup"

export default {

    props: ['project'],

    components: {
        draggable
    },

    created: function (){

        Vue.component('ErrorEditorPopup', ErrorEditorPopup);
    },

    data: function (){
        return {
            current_error: null
        }
    },

    methods: {

        addError: function (){
            
            this.$popups.open({

                title: this.$ml.get('create_error'),
                component: "ErrorEditorPopup",
                
                buttons: [
                    {
                        text: this.$ml.get('create_action'),
                        type: 'success',
                        callback: () => {

                            var component = this.$popups.getComponent();
                            var title = component.title;
                            var description = component.description;

                            this.$schema.addError(title, description);

                        }
                    },

                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'

                    }
                ]
                
            });
        },

        
        removeError: function (index){

            if(!this.$schema.errors[index]){
                return;
            }


            var error_object = this.$schema.errors[index];

            this.$popups.open({

                title: this.$ml.get('remove_error'),
                html: this.$ml.with('error_code', error_object.code).get('want_remove_error'),
                buttons: [
                    {
                        text: this.$ml.get('remove_action'),
                        type: "warning",
                        callback: () => {

                            if(this.current_error == index){
                                this.current_error = null;
                            }

                            this.$schema.removeError(index);

                        }
                    },
                    {
                        text: this.$ml.get('cancel'),
                        type: "cancel"
                    }
                ]

            });

        
        },

        openError: function (index){
            this.current_error = index;
        },

        getMethodsWithError: function (index){

            var error_code = this.$schema.errors[index].code;

            var list = [];

            for(var i = 0; i < this.$schema.classes.length; i++){

                var class_name = this.$schema.classes[i].name;
    
                for(var j = 0; j < this.$schema.classes[i].methods.length; j++){

                    var method_name = this.$schema.classes[i].methods[j].name;

                    if( this.$schema.classes[i].methods[j].errors.indexOf(error_code) != -1){
                        list.push({
                            class_name: class_name,
                            method_name: method_name
                        });
                    }

                }

            }

            return list;


        },

        removeErrorFromMethod: function (class_name, method_name, error_index){

            var error_code = this.$schema.errors[error_index].code;

            for(var i = 0; i < this.$schema.classes.length; i++){

                if(this.$schema.classes[i].name != class_name){
                    continue;
                }

                window.console.log(class_name);


                for(var j = 0; j < this.$schema.classes[i].methods.length; j++){

                    if(this.$schema.classes[i].methods[j].name != method_name){
                        continue;
                    }


                    var index = this.$schema.classes[i].methods[j].errors.indexOf(error_code);

                    if(index > -1){
                        Vue.delete(this.$schema.classes[i].methods[j].errors, index);
                        return;
                    }

                }

            }

        }
    }

}

</script>