import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

import ChooseProject from "@/components/pages/ChooseProject"
import Schema from "@/components/pages/Schema"
import Types from "@/components/pages/Types"
import Responses from "@/components/pages/Responses"
import Errors from "@/components/pages/Errors"

const routes = [
	{ name: 'choose-project', path: '/', component: ChooseProject },
	{ name: 'schema', path: '/schema', component: Schema, props: (route) => ({ project: route.query.project }) },
	{ name: 'types', path: '/types', component: Types, props: (route) => ({ project: route.query.project })  },
	{ name: 'responses', path: '/responses', component: Responses, props: (route) => ({ project: route.query.project })  },
	{ name: 'errors', path: '/errors', component: Errors, props: (route) => ({ project: route.query.project })  }
];

const router = new VueRouter({
	base: process.env.BASE_URL,
	mode: 'history',
	routes: routes,

	// eslint-disable-next-line
	scrollBehavior (to, from, savedPosition) {
		return { x: 0, y: 0 }
	}
});


export default router;