<template>

    <div class="date_picker">

        <dropdown class="day" placeholder="День" :items="days" is_search="true" v-model="d"/>
        <dropdown class="month"  placeholder="Месяц" :items="months" is_search="true" v-model="m"/>
        <dropdown class="year"  placeholder="Год" :items="years" is_search="true" v-model="y"/>

    </div>

</template>

<script>

export default {

    props: ['date'],

    created: function (){

        if(this.date){

            var y = parseInt(this.date / 10000);
            var m = (parseInt(this.date / 100) * 100 - y * 10000) / 100;
            var d = this.date - y * 10000 - m * 100;


            this.d = d;
            this.m = m;
            this.y = y;

        }

        this.updateDays();

    },

    data: function (){

        return {

            d: 1,
            m: 1,
            y: 2019,

            days: [],

            months: [
                {
                    text: "Январь",
                    value: 1

                },
                {
                    text: "Февраль",
                    value: 2
                },
                {
                    text: "Март",
                    value: 3
                },
                {
                    text: "Апрель",
                    value: 4
                },
                {
                    text: "Май",
                    value: 5
                },
                {
                    text: "Июнь",
                    value: 6
                },
                {
                    text: "Июль",
                    value: 7
                },
                {
                    text: "Август",
                    value: 8
                },
                {
                    text: "Сентябрь",
                    value: 9
                },
                {
                    text: "Октябрь",
                    value: 10
                },
                {
                    text: "Ноябрь",
                    value: 11
                },
                {
                    text: "Декабрь",
                    value: 12
                }
            ]


        }

    },

    watch: {

        d: function (){

            this.$emit('change', this.getDate());
        },
        
        m: function (){
            this.updateDays();

            this.$emit('change',  this.getDate());
        },

        y: function (){
            this.updateDays();
            this.$emit('change',  this.getDate());
        }

    },

    methods: {

        getDate: function (){
            return this.y * 10000 + this.m * 100 + this.d;
        },
        
        updateDays: function (){
            var arr31 = [1, 3, 5, 7, 8, 10, 12];
            var max_day = 31;
            

            if(arr31.indexOf(this.m) == -1){
                max_day = 30;
            }

            if(this.m == 2){
                if(this.y % 4 == 0){
                    max_day = 29;
                } else {
                    max_day = 28;
                }
            }

            if(this.d > max_day){
                this.d = max_day;
            }


            var array = new Array(max_day);
            
            for(var i = 0; i < array.length; i++){
                
                array[i] = {
                    text: (i + 1).toString(),
                    value: i + 1
                };


            }

            this.days = array;
        },

    },

    computed: {

        years: function (){
            
            var currentYear = new Date().getFullYear();            
            var startYear = 1899;  
            var array = new Array(currentYear - startYear);

            for(var i = 0; i < array.length; i++){
                array[i] = {
                    text: (currentYear - i).toString(),
                    value: (currentYear - i)
                };
            }

            return array;
        }

    }

}

</script>

<style>

.date_picker {
    display: flex;
    flex-direction: row;
}

.date_picker .day {
    width: 25%;
}

.date_picker .month {
    width: 35%;
    margin: 0px 5%;
}

.date_picker .year{
    width: 30%;
}

</style>