<template>

    <div class="tarea" :class="emoji ? 'emoji' : ''">
        
        <EmojiPicker class="emoji_picker" v-if="emoji" :textarea="$refs.tarea" @pick="pasteEmoji"/>

        <div contenteditable="true" ref="tarea" class="content" :class="virutalValue.length > 0 ? 'hide' : ''" @input="input"  @focus="focus" @blur="blur" :placeholder="placeholder ? placeholder : ''" @paste="paste" @drop="drop"></div>

    </div>

</template>

<style scoped>
.tarea {
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
    color: #000000;
    border: 1px solid #DCDCDC;
    border-radius: 3px;
    cursor: text;
}

.tarea .content {
    padding: 10px;
    flex: 1 1 auto;
    font-size: 13px;
    line-height: 20px;
}

.tarea.emoji .content {
    padding-right: 40px;
}


.tarea .content::before {
    content: attr(placeholder);
    visibility: visible;
    opacity: 1;
    position: absolute;
    color: #808080;
    cursor: text;
    height: 0px;
    transition: visibility linear 0.07s, opacity linear 0.07s, margin-left linear 0.07s;
}

.tarea .content.hide::before {
    visibility: hidden;
    opacity: 0;
    margin-left: 20px;
    transition-delay: 0s;
}

.tarea .emoji_picker {
    margin: 10px;
    margin-left: auto;
    margin-bottom: -30px;
}

</style>

<script>

export default {

    model: {
        prop: 'val',
        event: 'change'
    },

    props: ['val', 'placeholder', 'emoji'],

    mounted:function(){

        this.$refs.tarea.innerText = this.val ? this.val : '';

    },

    data: function (){

        return {

            virutalValue: ''

        }

    },

    watch: {

        val: function (data){

            this.virutalValue = data;

        }

    },

    methods: {


        pasteEmoji: function (emoji){

            this.$refs.tarea.innerHTML += `<img src="https://officialquack.github.io/emoji/icons/svg/${emoji.code}.svg" alt="${emoji.character}" width=20 height=20 align="top"/>`;
            this.virutalValue += emoji.character;

        },

        focus: function (e){

            this.$emit('focus', e);

        },

        blur: function (e){

            this.$emit('blur', e);

        },

        paste: function (e){
            
            e.preventDefault();

            var text = '';

            if (e.clipboardData || e.originalEvent.clipboardData) {
                
                text = (e.originalEvent || e).clipboardData.getData('text/plain');

            } else if (window.clipboardData) {

                text = window.clipboardData.getData('Text');

            }

            text = this.replaceEmojiToImg(text);
            
            if (document.queryCommandSupported('insertHtml')) {

                document.execCommand('insertHtml', false, text);

            } else {

                document.execCommand('paste', false, text);

            }

            this.updateValue();

        },

        replaceEmojiToImg: function (text){
            
            var emoji_regex = /([\u{1f300}-\u{1f5ff}\u{1f900}-\u{1f9ff}\u{1f600}-\u{1f64f}\u{1f680}-\u{1f6ff}\u{2600}-\u{26ff}\u{2700}-\u{27bf}\u{1f1e6}-\u{1f1ff}\u{1f191}-\u{1f251}\u{1f004}\u{1f0cf}\u{1f170}-\u{1f171}\u{1f17e}-\u{1f17f}\u{1f18e}\u{3030}\u{2b50}\u{2b55}\u{2934}-\u{2935}\u{2b05}-\u{2b07}\u{2b1b}-\u{2b1c}\u{3297}\u{3299}\u{303d}\u{00a9}\u{00ae}\u{2122}\u{23f3}\u{24c2}\u{23e9}-\u{23ef}\u{25b6}\u{23f8}-\u{23fa}])([[\u{1f3fb}-\u{1f3fc}\u{1f3fd}-\u{1f3fe}\u{1f3ff}]?)(\u{200D}?)/uig;

            text = text.replace(emoji_regex, function (match, p1, p2){
                
                var emoji_code;
                var emoji_main = p1.codePointAt(0).toString(16);

                if(p2){
                    
                    var emoji_tone_code = p2.codePointAt(0).toString(16);
                    emoji_code = emoji_main + (p2 ? '-' + emoji_tone_code : '');

                } else {

                    emoji_code = emoji_main;

                }

                return `<img src="https://officialquack.github.io/emoji/icons/svg/${emoji_code}.svg" alt="${p1}${p2}" width=20 height=20 align="top"/>`;

            });

            var modifier = new RegExp("\ud83c[\udffb-\udfff]", "ug");

            text = text.replace(modifier, "");

            return text;
        },
        
        drop: function (e){
            
            e.preventDefault();

        },

        input: function (){
            
            this.updateValue();

        },

        updateValue: function (){

            if(this.$refs.tarea.innerText == "\n"){
                this.$refs.tarea.innerText = "";
            }

        }

    }
}

</script>