<template>

    <div class="choose-error">
        
        <Dropdown v-model="error" :items="errors" :placeholder="`${$ml.get('choose_error')}...`" />

    </div>

</template>

<script>

export default {

    data: function (){

        return {
            error: -1
        }
    },

    computed: {

        errors: function (){

            var schema = this.$store.state.projects.find((element) => {
           
                return element.project.id == parseInt( this.$store.state.current_project);

            });

            return schema.errors.map(function (error){
                return {
                    text: `#${error.code} ${error.title}`,
                    value: error.code
                }
            })

        }

    }

}

</script>