<template>

    <div class="create-class">

        <div class="field">
            <div class="title">
                {{ $ml.get('class_name') }}
            </div>
            <input class="input" type="text" v-focus :placeholder="$ml.get('enter_class_name') + '...'" v-model="name" />
        </div>

        <div class="field">
            <div class="title">
                {{ $ml.get('description') }}
            </div>
            <div class="descriptions">
                <div class="description" v-for="(description, index) in descriptions" :key="description.lang">
                    <div class="subtitle">
                        <Dropdown v-model="description.lang" :items="languages" :placeholder="`${$ml.get('choose_language')}...`"/>
                        <div class="func">
                            <i class="icon fa fa-times" @click="removeDescription(index)"></i>
                        </div>
                    </div>
                    <Editor :height="'200px'" :options="markdown_options" mode="markdown" v-model="description.text"/>
                </div>
                <button class="cancel add_button" @click="addDescription"><i class="icon fa fa-plus"></i>{{ $ml.get('add_description') }}</button>
            </div>
        </div>

    </div>

</template>

<style lang="scss" scoped>

.create-class {
    
    display: flex;
    flex-direction: column;

    .field {
        display: flex;
        flex-direction: column;
        margin-bottom: 15px;

        &:last-child {
            margin-bottom: 0px;
        }

        .title {
            font-size: 14px;
            margin-bottom: 10px;
            font-weight: 500;
        }

        .subtitle {
            display: flex;
            flex-direction: row;
            align-items: center;
            font-size: 13px;
            margin-bottom: 10px;

            .func {
                margin-left: auto;

                .icon {
                    color: #808080;
                    font-size: 14px;
                    cursor: pointer;

                    &:hover {
                        color: #5b96cd;
                    }

                }
            }

        }

        .descriptions {
            display: flex;
            flex-direction: column;
            width: auto;

            .description {
                display: flex;
                flex-direction: column;
                width: auto;
                margin-bottom: 10px;
            }

            .add_button {
                width: auto;

                .icon {
                    margin-right: 7px;
                }
            }
        }

        .input {
            font-size: 13px;
        }

        .textarea {
            width: auto;
            padding: 15px;
            border-radius: 3px;
            font-size: 13px;
        }

    }

}

</style>

<script>

import Vue from 'vue'

import 'tui-editor/dist/tui-editor.css';
import 'tui-editor/dist/tui-editor-contents.css';
import 'codemirror/lib/codemirror.css';
import { Editor } from '@toast-ui/vue-editor'

export default {
 
    props: ['class_object'],

    components: {
        Editor
    },

    data: function (){

        return {
            name: "",
            descriptions: [],

            languages: [
                {
                    text: this.$ml.get('ru'),
                    value: 'ru'
                },
                {
                    text: this.$ml.get('en'),
                    value: 'en'
                }
            ],

            markdown_options: {
                useCommandShortcut: true,
                useDefaultHTMLSanitizer: true,
                usageStatistics: true,
                hideModeSwitch: true,
                toolbarItems: [
                    'heading',
                    'bold',
                    'italic',
                    'strike',
                    'ul',
                    'ol'
                ]
            }
            
        }

    },

    created: function (){

        if(!this.$props.class_object){
            return;
        }

        this.name = this.$props.class_object.name;
        this.descriptions = JSON.parse(JSON.stringify(this.$props.class_object.descriptions));

    },

    methods: {

        addDescription: function (){

            this.descriptions.push({
                lang: 'en',
                text: ''
            });

        },

        removeDescription: function (index){
            
            Vue.delete(this.descriptions, index);

        }

    }
    
}

</script>