<template>
    <div class="json-response-parser">

        <div class="field">
            <div class="title">
                {{ $ml.get('json_data') }}
            </div>
            <textarea class="textarea" v-focus :placeholder="$ml.get('enter_json_data') + '...'" v-model="json"></textarea>
        </div>

    </div>
</template>

<style lang="scss" scoped>

.json-response-parser {

    display: flex;
    flex-direction: column;

    @import '@/styles/popup-fields.scss';

}

</style>

<script>

export default {

    created: function (){

        this.$watch('json', this.parseJSONToSchema);

    },

    data: function (){

        return {
            response_schema: '',
            json: ''
        }

    },

    methods: {

        parseJSONToSchema: function (){

            var parsed;
            try {
                parsed = JSON.parse(this.json);

            } catch (e) {
                window.console.log(e);
                return;
            }

            this.response_schema = this.parseJSONObject(parsed);

        },

        parseJSONObject: function (object){

            var keys = Object.keys(object);
            var fields = [];

            for(var i = 0; i < keys.length; i++){

                var field = this.parseJSONField(object[keys[i]]);
                
                field.name = keys[i];
                

                fields.push(field);
            }

            window.console.log(fields);

            return fields;

        },

        parseJSONArray: function (array){

            var field_object = {
                name: '',
                type: 'int',
                descriptions: [],
                is_array: true,
                optional: false,
                fields: []
            };

            if(array.length == 0){
                return field_object;
            }

            var first_elem = array[0];

            var first_elem_field = this.parseJSONField(first_elem);

            field_object.type = first_elem_field.type;
            field_object.fields = first_elem_field.fields;

            return field_object;


        },

        parseJSONField: function (field){

            var field_object = {
                name: '',
                type: typeof field,
                descriptions: [],
                is_array: false,
                optional: false
            }
            
            switch(field_object.type){

                case "number":
                    
                    field_object.type = "int";

                    break;
                case "string":
                    break;
                case "object":


                    if(Array.isArray(field)){
                        field_object = this.parseJSONArray(field);
                        return field_object;
                    }

                    field_object.fields = this.parseJSONObject(field);

                    break;
            }

            return field_object;

        }

    }

}

</script>