<template>

    <div class="add-baseclass">
        
        <Dropdown v-model="type" :items="types" :placeholder="`${$ml.get('choose_type_field')}...`" />

    </div>

</template>

<script>

export default {

    props: ['baseclass'],

    created: function (){

        if(this.baseclass){
            this.type = this.baseclass;
        }

    },

    data: function (){

        return {
            type: -1
        }
    },

    computed: {

        types: function (){

            var schema = this.$store.state.projects.find((element) => {
           
                return element.project.id == parseInt( this.$store.state.current_project);

            });

            return schema.types.map(function (type){
                return {
                    text: type.name,
                    value: type.name
                }
            })

        }

    }

}

</script>