<template>

    <div class="responses">

        <!-- CLASSES -->
        <div class="column">

            <div class="title">
                <div class="text">{{ $ml.get('classes') }}</div>
            </div>

            <div class="list">
                <draggable v-model="$schema.classes" group="classes" :options="{ ghostClass: 'ghost' }">
                    <div class="item" :class="index == current_class ? 'choose' : '' " v-for="(class_object, index) in $schema.classes" :key="class_object.name" @click="openClass(index)">

                        <div class="name">    
                            {{class_object.name}}
                        </div>
                        
                    </div>
                </draggable>
            </div>

        </div>

        <!-- METHODS -->
        <div class="column">
            
            <div class="title">
                <div class="text">{{ $ml.get('methods') }}</div>
            </div>

            <div class="list" v-if="current_class != null">
                <draggable v-model="$schema.classes[current_class].methods" group="methods">
                    <div class="item" :class="index == current_method ? 'choose' : '' " v-for="(method_object, index) in $schema.classes[current_class].methods" :key="method_object.name" @click="openMethod(current_class, index)">

                        <div class="name">    
                            {{method_object.name}}
                        </div>

                        <div class="info">
                            <i class="icon fa fa-lock" v-if="method_object.is_auth"></i>
                            <i class="icon fa fa-shield-alt" v-if="method_object.is_admin"></i>
                            <i class="icon fa fa-database" v-if="method_object.is_db"></i>
                        </div>
                                                
                    </div>
                </draggable>
            </div>



        </div>

        <!-- RESPONSES -->
        <!-- Fields list -->
        <div class="column" v-if="current_class != null && current_method != null">
            <div class="title">
                <div class="text" :class="back_stack.length > 0 ? 'clickable' : ''" @click="goToBack"><i class="back_btn fa fa-arrow-left" v-if="back_stack.length > 0"></i>{{ $ml.get('type_fields') }}</div>
                <div class="controls">
                    <div class="control" @click="addField(current_class, current_method, back_stack)">
                        <i class="icon fa fa-plus"></i>
                    </div>
                    <div class="control" @click="addBaseClass(current_class, current_method, back_stack)">
                        <i class="icon fa fa-shapes"></i>
                    </div>
                    <div class="control" @click="openJSONResponseParser(current_class, current_method, back_stack)">
                        <i class="icon fa fa-brackets-curly"></i>
                    </div>
                </div>
            </div>

            <div class="list">

                <div class="back_stack" v-if="back_stack.length > 0">
                    <div class="stack_item" @click="goToStack(0)"><div class="name">{{ $schema.classes[current_class].methods[current_method].name }}</div></div>
                    <div class="stack_item" v-for="(item, index) in back_stack" :key="item + ' ' + index" @click="goToStack(index + 1)"> 
                        <i class="icon icon-arrow-right-line"></i>
                        <div class="name">{{ getField(current_class, current_method, back_stack.slice(0, index), item).name }}</div>
                    </div>
                </div>

                <div class="item" v-if="getBaseClass(current_class, current_method, back_stack) != null" @click="editBaseClass(current_class, current_method, back_stack)">

                        <div class="name"><i class="icon fa fa-shapes"></i>{{ getBaseClass(current_class, current_method, back_stack) }} — {{ $ml.get('baseclass') }}</div>
                        <div class="controls">

                            <div class="control">
                                <i class="icon fa fa-cog"  @click.stop="editBaseClass(current_class, current_method, back_stack)"></i>
                            </div>

                            <div class="control">
                                <i class="icon fa fa-times"  @click.stop="removeBaseClass(current_class, current_method, back_stack)"></i>
                            </div>
                        </div>

                </div>

                <draggable v-model="currentFields" group="fields">
                    <div class="item" v-for="(field, index) in getFields(current_class, current_method, back_stack)" :key="field.name" @click="field.type == 'object' ? openObjectField(current_class, current_method, back_stack, index) : null">

                        <div class="name">{{ field.name }}</div>
                        <div class="info">
                            <span class="icon">{{ field.type }}{{ field.is_array ? ' [ ]' : '' }}</span>
                            <i v-if="field.optional" class="icon far fa-question-circle"></i>
                        </div>
                        <div class="controls">

                            <div class="control">
                                <i class="icon fa fa-cog"  @click.stop="editField(current_class, current_method, back_stack, index)"></i>
                            </div>

                            <div class="control">
                                <i class="icon fa fa-times"  @click.stop="removeField(current_class, current_method, back_stack, index)"></i>
                            </div>
                        </div>

                    </div>
                </draggable>

            </div>

        </div>
    </div>

</template>

<script>

import Vue from 'vue'
import draggable from 'vuedraggable'
import BaseClassEditorPopup from '@/components/popups/BaseClassEditorPopup'
import FieldEditorPopup from '@/components/popups/FieldEditorPopup'
import JSONResponseParserPopup from '@/components/popups/JSONResponseParserPopup'

export default {

    props: ['project'],

    components: {
        draggable
    },

    created: function (){

        Vue.component('FieldEditorPopup', FieldEditorPopup);
        Vue.component('BaseClassEditorPopup', BaseClassEditorPopup);
        Vue.component('JSONResponseParserPopup', JSONResponseParserPopup);

    },

    data: function (){

        return {

            schema: null,

            current_class: null,
            current_method: null,
            back_stack: []

        }

    },

    computed: {

        currentFields: {

            get: function (){

                return this.getFields(this.current_class, this.current_method, this.back_stack);

            },

            set: function (fields){

                window.console.log(fields);

                var local_fields = this.$schema.classes[this.current_class].methods[this.current_method].response;

                for(var i = 0; i < this.back_stack.length; i++){

                    local_fields = local_fields.fields[this.back_stack[i]];

                }

                local_fields.fields = fields;

            }

        }

    },

    methods: {
        
        openClass: function (class_index){

            if(class_index == this.current_class) return;

            this.current_method = null;
            this.current_class = class_index;
            this.back_stack = [];
        },

        openMethod: function (class_index, method_index){

            if(class_index == this.current_class && method_index == this.current_method) return;

            this.current_class = class_index;
            this.current_method = method_index;
            this.back_stack = [];
        },

        // WORK WITH FIELDS

        getBaseClass: function (class_index, method_index, back_stack){
                
            var local_fields = this.$schema.classes[class_index].methods[method_index].response;

            for(var i = 0; i < back_stack.length; i++){

                local_fields = local_fields.fields[back_stack[i]];

                window.console.log(local_fields);

            }

            var base = local_fields.base;

            if(!base){
                return null;
            }

            return base;
        },

        getField: function (class_index, method_index, back_stack, index){
            var fields = this.getFields(class_index, method_index, back_stack);

            return fields[index];
        },

        getFields: function (class_index, method_index, back_stack){

            var local_fields = this.$schema.classes[class_index].methods[method_index].response.fields;

            for(var i = 0; i < back_stack.length; i++){

                local_fields = local_fields[back_stack[i]].fields;

            }


            return local_fields;

        },

        addField: function (class_index, method_index, back_stack){

            this.$popups.open({

                title: this.$ml.get('create_field'),
                component: "FieldEditorPopup",
                data: {
                    user_types: this.$schema.types
                },

                buttons: [
                    {
                        text: this.$ml.get('save_action'),
                        type: 'success',
                        callback: () => {

                            var component = this.$popups.getComponent();
                            var name = component.name;
                            var type = component.type;
                            var descriptions = component.descriptions;
                            var is_array = component.is_array;
                            var optional = component.optional;

                            this.$schema.addResponseField(class_index, method_index, back_stack, name, type, descriptions, is_array, optional);
                            
                        }
                    },

                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'

                    }
                ]
                
            });


        },

        editField: function (class_index, method_index, back_stack, index){

            var field = this.getField(class_index, method_index, back_stack, index);
            
            this.$popups.open({

                title: this.$ml.get('edit_field'),
                component: "FieldEditorPopup",
                data: {
                    field_object: field,
                    user_types: this.$schema.types
                },

                buttons: [
                    {
                        text: this.$ml.get('create_action'),
                        type: 'success',
                        callback: () => {

                            var component = this.$popups.getComponent();
                            var name = component.name;
                            var descriptions = component.descriptions;
                            var type = component.type;
                            var is_array = component.is_array;
                            var optional = component.optional;

                            this.$schema.editResponseField(class_index, method_index, back_stack, index, name, type, descriptions, is_array, optional);


                        }
                    },

                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'

                    }
                ]
                
            });

        },

        removeField: function (class_index, method_index, back_stack, index){
            
            var field = this.getField(class_index, method_index, back_stack, index);

            this.$popups.open({

                title: this.$ml.get('remove_field'),
                html: this.$ml.with('field_name', field.name).get('want_remove_field'),
                
                buttons: [

                    {
                        text: this.$ml.get('remove_action'),
                        type: 'warning',
                        callback: () => {

                            this.$schema.removeResponseField(class_index, method_index, back_stack, index);
                        }
                    },

                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'

                    }
                ]
            });
        },

        addBaseClass: function (class_index, method_index, back_stack){
            
            this.$popups.open({

                title: this.$ml.get('create_field'),
                component: "BaseClassEditorPopup",
                
                buttons: [
                    {
                        text: this.$ml.get('create_action'),
                        type: 'success',
                        callback: () => {

                            var component = this.$popups.getComponent();
                            var type = component.type;

                            this.$schema.addBaseClass(class_index, method_index, back_stack, type);
                            
                        }
                    },

                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'

                    }
                ]
                
            });
        },

        editBaseClass: function (class_index, method_index, back_stack){

            this.$popups.open({

                title: this.$ml.get('create_field'),
                component: "BaseClassEditorPopup",
                data: {

                },
                
                buttons: [
                    {
                        text: this.$ml.get('save_action'),
                        type: 'success',
                        callback: () => {

                            var component = this.$popups.getComponent();
                            var type = component.type;

                            if(type == -1){
                                return;
                            }

                            this.$schema.editBaseClass(class_index, method_index, back_stack, type);
                            
                        }
                    },

                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'

                    }
                ]
                
            });
        },

        removeBaseClass: function (class_index, method_index, back_stack){
            
            this.$popups.open({

                title: this.$ml.get('remove_field'),
                html: this.$ml.get('want_remove_baseclass'),
                
                buttons: [
                    {
                        text: this.$ml.get('remove_action'),
                        type: 'warning',
                        callback: () => {

                            this.$schema.removeBaseClass(class_index, method_index, back_stack);
                        }
                    },

                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'

                    }
                ]
            });
        },

        openJSONResponseParser: function (class_index, method_index, back_stack){
            
             this.$popups.open({

                title: this.$ml.get('remove_field'),
                component: 'JSONResponseParserPopup',
                
                buttons: [
                    {
                        text: this.$ml.get('save_action'),
                        type: 'success',
                        callback: () => {

                            var response_schema = JSON.parse(JSON.stringify(this.$popups.getComponent().response_schema));

                            var local_fields = this.$schema.classes[class_index].methods[method_index].response;

                            for(var i = 0; i < back_stack.length; i++){

                                local_fields = local_fields.fields[back_stack[i]];

                            }

                            window.console.log(response_schema);

                            window.console.log(local_fields);

                            local_fields.fields = response_schema;
                        }
                    },

                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'

                    }
                ]
            });

        },

        openObjectField: function (class_index, method_index, back_stack, index){

            this.back_stack.push(index);

        },

        goToBack: function (){

            this.back_stack.pop();

        },


        goToStack: function (index){
            this.back_stack = this.back_stack.slice(0, index);
        },
    }

}

</script>

<style lang="scss" scoped>

.responses {

    display: flex;
    flex-direction: row;
    flex: 1;
    min-height: 100%;

    @import '@/styles/columns.scss';

}
</style>
