<template>

    <div class="types">

        <!-- Types list -->
        <div class="column">
            <div class="title">
                <div class="text">{{ $ml.get('types') }}</div>
                <div class="controls">
                    <div class="control" @click="addType">
                        <i class="icon fa fa-plus"></i>
                    </div>
                </div>
            </div>

            <div class="list">

                <draggable v-model="$schema.types" group="types">
                    <div class="item" v-for="(type, index) in $schema.types" :class="index == current_type ? 'choose' : ''" :key="type.name" @click.stop="openType(index)">

                        <div class="name">{{ type.name }}</div>
                        <div class="controls">

                            <div class="control">
                                <i class="icon fa fa-cog"  @click.stop="editType(index)"></i>
                            </div>

                            <div class="control">
                                <i class="icon fa fa-times"  @click.stop="removeType(index)"></i>
                            </div>
                        </div>

                    </div>
                </draggable>

            </div>
        </div>

        <!-- Fields list -->
        <div class="column" v-if="current_type != null">
            <div class="title">
                <div class="text" :class="back_stack.length > 0 ? 'clickable' : ''" @click="goToBack"><i class="back_btn fa fa-arrow-left" v-if="back_stack.length > 0"></i>{{ $ml.get('type_fields') }}</div>
                <div class="controls">
                    <div class="control" @click="addField(current_type, back_stack)">
                        <i class="icon fa fa-plus"></i>
                    </div>
                </div>
            </div>

            <div class="list">

                <div class="back_stack" v-if="back_stack.length > 0">
                    <div class="stack_item" @click="goToStack(0)"><div class="name">{{ $schema.types[current_type].name }}</div></div>
                    <div class="stack_item" v-for="(item, index) in back_stack" :key="item + ' ' + index" @click="goToStack(index + 1)"> 
                        <i class="icon icon-arrow-right-line"></i>
                        <div class="name">{{ getField(current_type, back_stack.slice(0, index), item).name }}</div>
                    </div>
                </div>

                <draggable v-model="currentFields" group="fields">
                    <div class="item" v-for="(field, index) in getFields(current_type, back_stack)" :key="field.name" @click="field.type == 'object' ? openObjectField(current_type, back_stack, index) : null">

                        <div class="name">{{ field.name }}</div>
                        <div class="info">
                            <span class="icon">{{ field.type }}{{ field.is_array ? ' [ ]' : '' }}</span>
                            <i v-if="field.optional" class="icon far fa-question-circle"></i>
                        </div>
                        <div class="controls">

                            <div class="control">
                                <i class="icon fa fa-cog"  @click.stop="editField(current_type, back_stack, index)"></i>
                            </div>

                            <div class="control">
                                <i class="icon fa fa-times"  @click.stop="removeField(current_type, back_stack, index)"></i>
                            </div>
                        </div>

                    </div>
                </draggable>

            </div>

        </div>
        
    </div>

</template>

<script>

import Vue from 'vue'
import draggable from 'vuedraggable'

import TypeEditorPopup from '@/components/popups/TypeEditorPopup'
import FieldEditorPopup from '@/components/popups/FieldEditorPopup'

export default {

    props: ['project'],

    components: {
        draggable
    },


    created: function (){
        
        Vue.component('TypeEditorPopup', TypeEditorPopup);
        Vue.component('FieldEditorPopup', FieldEditorPopup);

    },

    data: function (){

        return {
            current_type: null,
            back_stack: []
        }

    },

    computed: {

        currentFields: {

            get: function (){

                return this.getFields(this.current_type, this.back_stack);

            },

            set: function (fields){

                window.console.log(fields);

                var local_fields = this.$schema.types[this.current_type];

                for(var i = 0; i < this.back_stack.length; i++){

                    local_fields = local_fields.fields[this.back_stack[i]];

                }

                local_fields.fields = fields;

            }

        }

    },

    methods: {
        
        getField: function (type_index, back_stack, index){
            var fields = this.getFields(type_index, back_stack);

            return fields[index];
        },

        getFields: function (type_index, back_stack){

            var local_fields = this.$schema.types[type_index].fields;

            for(var i = 0; i < back_stack.length; i++){

                local_fields = local_fields[back_stack[i]].fields;

            }


            return local_fields;

        },
        addType: function (){
            
            this.$popups.open({

                title: this.$ml.get('create_type'),
                component: 'TypeEditorPopup',

                buttons: [

                    {
                        text: this.$ml.get('create_action'),
                        type: 'success',
                        callback: () => {

                            var name = this.$popups.getComponent().name;

                            this.$schema.addType(name);

                        }
                    },

                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'

                    }
                ]
            });

        },

        editType: function (index){
            
            this.$popups.open({

                title: this.$ml.get('edit_type'),
                component: 'TypeEditorPopup',
                
                data: {
                    type_object: this.$schema.types[index],
                },

                buttons: [

                    {
                        text: this.$ml.get('save_action'),
                        type: 'success',
                        callback: () => {

                            var name = this.$popups.getComponent().name;

                            this.$schema.editType(index, name);
                            
                        }
                    },

                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'

                    }
                ]
            });

        },

        removeType: function (index){

            var type = this.$schema.types[index];

            this.$popups.open({

                title: this.$ml.get('remove_type'),
                html: this.$ml.with('type_name', type.name).get('want_remove_type'),
                
                data: {
                    type_object: this.$schema.types[index],
                },

                buttons: [

                    {
                        text: this.$ml.get('remove_action'),
                        type: 'warning',
                        callback: () => {


                            if(this.current_index == index){
                                this.current_index = -1;
                            }

                            this.$schema.removeType(index);

                        }
                    },

                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'

                    }
                ]
            });
        },

        openType: function (index){


            if(index == this.current_type){
                return;
            }

            this.current_type = index;
            this.back_stack = [];

        },
        
        addField: function (type_index, back_stack){

            this.$popups.open({

                title: this.$ml.get('create_field'),
                component: "FieldEditorPopup",
                data: {
                    user_types: this.$schema.types.filter(function (type, index){
                        return index != type_index;
                    })
                },

                buttons: [
                    {
                        text: this.$ml.get('save_action'),
                        type: 'success',
                        callback: () => {

                            var component = this.$popups.getComponent();
                            var name = component.name;
                            var type = component.type;
                            var descriptions = component.descriptions;
                            var is_array = component.is_array;
                            var optional = component.optional;

                            this.$schema.addTypeField(type_index, back_stack, name, type, descriptions, is_array, optional);
                            
                        }
                    },

                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'

                    }
                ]
                
            });


        },

        editField: function (type_index, back_stack, index){

            var field = this.getField(type_index, back_stack, index);
            
            this.$popups.open({

                title: this.$ml.get('edit_field'),
                component: "FieldEditorPopup",
                data: {
                    field_object: field,
                    user_types: this.$schema.types
                },

                buttons: [
                    {
                        text: this.$ml.get('create_action'),
                        type: 'success',
                        callback: () => {

                            var component = this.$popups.getComponent();
                            var name = component.name;
                            var type = component.type;
                            var descriptions = component.descriptions;
                            var is_array = component.is_array;
                            var optional = component.optional;

                            this.$schema.editTypeField(type_index, back_stack, index, name, type, descriptions, is_array, optional);

                        }
                    },

                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'

                    }
                ]
                
            });

        },

        removeField: function (type_index, back_stack, index){
            
            var field = this.getField(type_index, back_stack, index);

            this.$popups.open({

                title: this.$ml.get('remove_field'),
                html: this.$ml.with('field_name', field.name).get('want_remove_field'),
                
                buttons: [

                    {
                        text: this.$ml.get('remove_action'),
                        type: 'warning',
                        callback: () => {

                            this.$schema.removeTypeField(type_index, back_stack, index);

                        }
                    },

                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'

                    }
                ]
            });
        },

        openObjectField: function (type_index, back_stack, index){

            window.console.log({ type_index, back_stack, index });
            this.back_stack.push(index);

        },

        goToBack: function (){

            this.back_stack.pop();

        },


        goToStack: function (index){
            this.back_stack = this.back_stack.slice(0, index);
        }

    }

}

</script>

<style lang="scss" scoped>

.types {
    display: flex;
    flex-direction: row;
    flex: 1;
    min-height: 100%;

    @import '@/styles/columns.scss';

}

</style>
