<template>

    <div class="type-editor">
        
        <div class="field">
            <div class="title">{{ $ml.get('type_name') }}</div>
            <input type="text" class="input" v-focus v-model="name" :placeholder="`${$ml.get('enter_type_name')}...`"/>
        </div>

    </div>

</template>

<script>

export default {

    props: ['type_object'],

    created: function (){

        if(this.$props.type_object){
            
            this.name = this.$props.type_object.name;
        }

    },

    data: function (){

        return {
            name: ""
        }

    }

}

</script>

<style lang="scss" scoped>

.type-editor {

    display: flex;
    flex-direction: column;

    .field {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0px;
        }

        .title {
            font-size: 14px;
            margin-bottom: 10px;
            font-weight: 500;
        }

        .input {
            font-size: 13px;
        }

        .textarea {
            width: auto;
            padding: 15px;
            border-radius: 3px;
            font-size: 13px;
        }
    }

