import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    
    state: {

        current_project: null,
        projects: null,

    },
    
    mutations: {


        setProjects: function (state, projects){

            state.projects = projects;

        },

        setCurrentProject: function (state, project_id){

            state.current_project = project_id;

        },

        addProject: function (state, project){

            state.projects.push(project);
            window.localStorage.setItem('projects', JSON.stringify(state.projects));

        },

        updateProject: function (state, project){

            for(var i = 0; i < state.projects.length; i++){

                if(state.projects[i].project.id == project.project.id){
                    state.projects[i] = project;
                    break;
                }

            }

            window.localStorage.setItem('projects', JSON.stringify(state.projects));

        }


    },

    actions: {

        addProject: function ({commit}, project){

            commit('addProject', project);

        },

        loadProjects: function ({commit}){
            
            var projects = window.localStorage.getItem('projects');

            if(!projects){
                projects = [];
                window.localStorage.setItem('projects', JSON.stringify(projects));
            } else {
                projects = JSON.parse(projects);
            }

            commit('setProjects', projects)
        

        },

        setCurrentProject: function ({commit}, project_id){

            commit('setCurrentProject', project_id);

        },

        updateProject: function ({commit}, project){

            commit('updateProject', project);

        }

    }
});