<template>

    <div class="settings-project">

        <div class="field">
            <div class="title">
                {{ $ml.get('project_name') }}
            </div>

            <input type="text" v-focus class="input" v-model="name" :placeholder="$ml.get('enter_name') + `...`"/>

        </div>

        <div class="field">
            <div class="title">
                {{ $ml.get('error_counter') }}
            </div>

            <input type="number" class="input" v-model.number="error_counter" :placeholder="$ml.get('enter_value') + `...`"/>

        </div>

        <div class="field"> 
            <div class="title">
                {{ $ml.get('remove_project') }}
            </div>


        </div>

    </div>

</template>

<script>

export default {

    props: ['project'],

    created: function (){

        if(this.project){
            this.name = this.project.name;
            this.error_counter = this.project.vars.error_counter;
        }

    },

    data: function (){
        return {
            name: "",
            error_counter: 0,
        }
    }

}

</script>

<style lang="scss" scoped>

.settings-project {
    
    display: flex;
    flex-direction: column;

    .field {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0px;
        }

        .title {
            font-size: 14px;
            margin-bottom: 10px;
            font-weight: 500;
        }

        .input {
            font-size: 13px;
        }

        .textarea {
            width: auto;
            padding: 15px;
            border-radius: 3px;
            font-size: 13px;
        }

        .checkbox {
            font-size: 14px;
            margin-top: 5px;
            margin-bottom: 15px;
        }

    }
}
</style>
