<template>

    <div class="emoji_picker" v-click-outside="closePopup">
        <div class="icon" @mouseover="openPopup">
            <i class="far fa-smile"></i>
        </div>
        <div class="emoji_popup"  :class="[is_open ? 'open' : '', this.position]">
            <div class="tabs">
                <div class="tab" @click="scrollToSection('recent')">
                    <i class="far fa-history"></i>
                </div>

                
                <div class="tab" v-for="(icon, section) in section_icons" :key="section" @click="scrollToSection(section)">
                    <i :class="icon"></i>
                </div>

            </div>

            <scroll-view class="list" ref="emoji_list">

                 <div class="section section-recent" v-if="recent_emoji.length > 0"  v-show="search_value.length == 0">

                    <div class="title">Recent emoji</div>

                    <div class="icons">

                        <div  class="emoji_container" v-for="icon in recent_emoji" :key="icon.name" @click="addToTextarea({ title: icon.section }, icon)">
                            <div :class="['emoji-24-' + (icon.code.split('-').length > 1 && icon.section != 'flags'  ? 'diversity' : icon.section), `_${icon.code}`]" >&nbsp;</div>
                        </div>

                    </div>

                </div>

                <div class="section" v-for="section in emoji" :key="section.id" :class="`section-${section.title}`"  v-show="search_value.length == 0">

                    <div class="title">{{ section.long_title }}</div>

                    <div class="icons">

                        <div  class="emoji_container" v-for="icon in section.emoji" :key="icon.name" @click="addToTextarea(section, icon)">
                            <div :class="['emoji-24-' + (icon.code.split('-').length > 1 && section.title != 'flags'  ? 'diversity' : section.title), `_${icon.code}`]" ></div>
                        </div>

                    </div>

                </div>

                <div class="section" v-for="section in search_list" :key="section.id" :class="`section-${section.title}`"  v-show="search_value.length > 0">

                    <div class="title">{{ section.long_title }}</div>

                    <div class="icons">

                        <div  class="emoji_container" v-for="icon in section.emoji" :key="icon.name" @click="addToTextarea(section, icon)">
                            <div :class="['emoji-24-' + (icon.code.split('-').length > 1 && section.title != 'flags' ? 'diversity' : section.title), `_${icon.code}`]" >&nbsp;</div>
                        </div>

                    </div>

                </div>
            </scroll-view>

            <div class="search">
                <i class="btn far fa-search"></i>
                <input class="search_field" type="text" placeholder="Поиск по смайликам..." v-model="search_value" />
                <i class="close btn fa fa-times" v-if="this.search_value.length > 0" @click="(e) => { this.search_value = '';  }"></i>
            </div>
        </div>
    </div>

</template>

<script>

import EmojiList from "./data/list.json"
import "./data/emoji-sprite-24.css"

export default {

    props: ['textarea'],

    created: function(){

        var recent_emoji = this.$cookie.get('emoji_recent');

        if(recent_emoji == null){

            this.$cookie.set('emoji_recent', '[]');

        } else {

            try {
                this.recent_emoji = JSON.parse(recent_emoji);
            } catch (e) {
                this.$cookie.set('emoji_recent', '[]');
            }

        }


    },

    data: function (){

        return {
            is_open: false,
            position: 'over',

            recent_emoji: [],

            emoji: EmojiList,
            search_list: [],
            search_value: '',

            section_icons: {
                people: 'far fa-smile',
                nature: 'far fa-paw',
                food: 'far fa-apple-alt',
                activity: 'far fa-futbol',
                travel: 'far fa-compass',
                objects: 'far fa-th-large',
                flags: 'far fa-flag',
            }
        }
    },

    watch: {

        recent_emoji: function (){

            this.$cookie.set('emoji_recent', JSON.stringify(this.recent_emoji));

        },

        search_value: function (){

            this.searchEmoji();

        }

    },

    methods: {

        scrollToSection: function (section){

            var elems = this.$refs.emoji_list.$el.getElementsByClassName('section-' + section);

            if(elems.length == 0){
                return;
            }

            var sectionNode = elems[0];

            this.$refs.emoji_list.animateScrollTo(sectionNode.offsetTop, 15);

        },

        searchEmoji: function (){

            if(this.search_value.trim().length == 0){
                return;
            }

            this.$refs.emoji_list.scrollTo(0);

            var search_string = this.search_value.toLowerCase();
            this.search_list = [];

            for(var i = 0; i < this.emoji.length; i++){

                var section = this.emoji[i];
                var newSection = {
                    title: section.title,
                    long_title: section.long_title,
                    emoji: []
                };

                for(var j = 0; j < section.emoji.length; j++){

                    var emoji = section.emoji[j];
                    var emoji_description = emoji.keywords.join(' ');
                    
                    if(emoji_description.indexOf(search_string) > -1){

                        newSection.emoji.push(emoji);

                    }

                }

                if(newSection.emoji.length > 0){
                    this.search_list.push(newSection);
                }
                

            }


        },

        addToRecentEmoji: function (emoji){

            var i = this.recent_emoji.map(function (item){

                return item.code;

            }).indexOf(emoji.code);

            if(i == 0){
                return;
            }
            
            if(i == -1){

                var mini_emoji = {
                    code: emoji.code,
                    name: emoji.name,
                    character: emoji.character,
                    section: emoji.section
                };

                this.recent_emoji.unshift(mini_emoji);

                if(this.recent_emoji.length > 16){
                    this.recent_emoji.pop();
                }

                return;
            }
            
            var tmp = this.recent_emoji[i - 1];
            this.$set(this.recent_emoji, i - 1, this.recent_emoji[i]);
            this.$set(this.recent_emoji, i, tmp);

        },

        addToTextarea: function (section, emoji){

            emoji.section = section.title;

            this.addToRecentEmoji(emoji);

            this.$emit('pick', emoji);

        },

        togglePopup: function (e){

            if(this.is_open){
                this.closePopup(e);
            } else {
                this.openPopup(e);
            }

        },


        openPopup: function (e){

            var btnPosition = e.target.getBoundingClientRect();

            if(btnPosition.top < 330){
                this.position = 'under';
            } else {
                this.position = 'over';
            }

            var self = this;
            setTimeout(function (){

                self.is_open = true;

            }, 150);
        },

        closePopup: function (){
            this.is_open = false;
        }
    }

}

</script>

<style>

.emoji_picker {
    display: flex;
    position: relative;
    cursor: default;
}

.emoji_picker .icon {
    width: 20px;
    height: 20px;
    cursor: pointer;
}

.emoji_picker .icon i {
    font-size: 20px;
    color: #b4b4b4;
}

.emoji_picker .icon:hover i {
    color: #5b96cd;
}

.emoji_picker .emoji_popup {
    visibility: hidden;
    opacity: 0;
    z-index: 9999;
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 270px;
    height: 300px;
    background-color: #ffffff;
    border: 1px solid #DCDCDC;
    border-radius: 3px;
    box-shadow: 0px 3px 3px #00000033;
    margin-left: -135px;
    transition: visibility linear 0.1s, opacity linear 0.1s, margin-top linear 0.1s;
}

.emoji_picker .emoji_popup.open {
    visibility: visible;
    opacity: 1;
}


.emoji_picker .emoji_popup.under {
    margin-top: 22px;
    transition-delay: 0s;
}

.emoji_picker .emoji_popup.under.open {
    margin-top: 32px;
}


.emoji_picker .emoji_popup.over {
    margin-top: -303px; 
    transition-delay: 0s;
}

.emoji_picker .emoji_popup.over.open {
    margin-top: -313px;
}

.emoji_picker .emoji_popup.under::before {
    content: '';
    width: 0; 
    height: 0; 
    position: absolute;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;    
    border-bottom: 8px solid #DCDCDC;
    left: 50%;
    margin-top: -8px;
}

.emoji_picker .emoji_popup.under::after {
    content: '';
    position: absolute;
    width: 0; 
    height: 0; 
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;    
    border-bottom: 7px solid #ffffff;
    margin-left: 1px;
    left: 50%;
    margin-top: -6px;
}

.emoji_picker .emoji_popup.over::before {
    content: '';
    width: 0; 
    height: 0; 
    position: absolute;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;    
    border-top: 8px solid #DCDCDC;
    left: 50%;
    top: 100%;
}

.emoji_picker .emoji_popup.over::after {
    content: '';
    position: absolute;
    width: 0; 
    height: 0; 
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;    
    border-top: 7px solid #ffffff;
    margin-left: 1px;
    left: 50%;
    top: 100%;
}

.emoji_picker .emoji_popup .list {
    display: flex;
    flex-direction: column;
    overflow-y: auto;
}

.emoji_picker .emoji_popup .list .section {
    display: flex;
    flex: 1;
    flex-direction: column;
}

.emoji_picker .emoji_popup .list .section .title {
    color: #808080;
    padding: 10px;
    padding-bottom: 5px;
    font-size: 14px;
}

.emoji_picker .emoji_popup .list .section .icons {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 5px 10px;
}

.emoji_picker .emoji_popup .list .section .icons .emoji_container {
    display: flex;
    width: 12.5%;
    margin: 3px 0px;
    justify-content: center;
    cursor: pointer;
}


.emoji_picker .emoji_popup .tabs {
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #DCDCDC;
    padding: 5px;
}

.emoji_picker .emoji_popup .tabs .tab {
    padding: 7px;
    cursor: pointer;
}

.emoji_picker .emoji_popup .tabs .tab i {
    color: #b4b4b4;
    font-size: 19px;
}

.emoji_picker .emoji_popup .tabs .tab:hover i {
    color: #5b96cd;
}


.emoji_picker .emoji_popup .search {
    display: flex;
    flex-direction: row;
    border-top: 1px solid #DCDCDC;
}

.emoji_picker .emoji_popup .search .btn {
    padding: 13px;
    color: #808080;
    font-size: 13px;
}

.emoji_picker .emoji_popup .search .btn.close {
    cursor: pointer;
}

.emoji_picker .emoji_popup .search .search_field {
    flex: 1;
    border: none;
    padding: 13px;
    padding-left: 0px;
    font-size: 12px;
}





</style>