<template>

    <div class="nav" v-show="this.$route.name != 'choose-project'">

        <router-link class="logo" to="/">
            <i class="icon-quack"></i><div>API Generator</div>
        </router-link>

        <div class="sections">
            <router-link :class="$route.name == 'schema' ? 'choose' : ''" :to="`/schema?project=${$store.state.current_project}`"><i class="icon fa fa-project-diagram"></i></router-link>
            <router-link :class="$route.name == 'types' ? 'choose' : ''" :to="`/types?project=${$store.state.current_project}`"><i class="icon fa fa-shapes"></i></router-link>
            <router-link :class="$route.name == 'responses' ? 'choose' : ''" :to="`/responses?project=${$store.state.current_project}`"><i class="icon fa fa-reply"></i></router-link>
            <router-link :class="$route.name == 'errors' ? 'choose' : ''" :to="`/errors?project=${$store.state.current_project}`"><i class="icon fa fa-exclamation-circle"></i></router-link>
        </div>

        <div class="controls">
            <div class="control" @click="downloadProject"><i class="icon fa fa-download"></i></div>
            <div class="control" @click="uploadProject"><i class="icon fa fa-upload"></i></div>
            <div class="control" @click="openTemplates"><i class="icon fa fa-cubes"></i></div>
            <div class="control" @click="openSettingsProject"><i class="icon fa fa-cog"></i></div>
            
        </div>

    </div>

</template>

<style lang="scss">

* {
    font-family: 'Roboto', sans-serif;
}

html, body {
    height: 100%;
}

.center {

    margin: 0 auto;
    width: 1280px;
    min-height: 100%;

    & > .nav {
        display: flex;
        align-items: center;
        height: 100px;
        border-bottom: 1px solid #DCDCDC;

        & > .logo {
            display: flex;
            min-width: 230px;
            height: 40px;
            margin-right: 20px;
            line-height: 40px;
            text-decoration: none;
            font-weight: 500;
            overflow: hidden;
            color: #5b96cd;
            font-size: 25px;

            & > .icon-quack {

                font-size: 35px;
                margin-right: 15px;

            }

        }

        & > .sections {

            margin: 0 auto;
            display: flex;
            flex-direction: row;


            & > a {

                width: 40px;
                height: 40px;
                padding: 10px;
                font-size: 25px;
                text-align: center;
                line-height: 40px;
                color: #808080;
                margin: 15px;

                &:hover, &.choose {
                    color: #5b96cd;
                }

            }

        }

        & > .controls {
            display: flex;
            flex-direction: row;
            min-width: 230px;

            & > .control {
                font-size: 20px;
                color: #808080;
                margin-right: 40px;
                cursor: pointer;

                &:first-child {
                    margin-left: auto;
                }

                &:hover {
                    color: #5b96cd;
                }

            }


        }
    }

        
}

</style>


<script>

import Vue from 'vue'
import TemplateListPopup from '@/components/popups/TemplateListPopup'
import TemplateEditorPopup from '@/components/popups/TemplateEditorPopup'
import SettingsProjectPopup from '@/components/popups/SettingsProjectPopup'

export default {

    created: function (){

        if(this.$route.query.project){

            this.$store.dispatch('setCurrentProject', this.$route.query.project);
            this.$schema.loadSchemaById(this.$route.query.project);

        }

        Vue.component('SettingsProjectPopup', SettingsProjectPopup);        
        Vue.component('TemplateListPopup', TemplateListPopup);
        Vue.component('TemplateEditorPopup', TemplateEditorPopup);

        this.$watch(
            'schema',

            function (){
                this.$store.dispatch('updateProject', this.schema);
            },

            { deep: true }
        );

    },

    methods: {
        
        openTemplates: function (){

            this.$popups.open({
                
                title: this.$ml.get('template_list'),
                component: "TemplateListPopup"

            });

        },

        uploadProject: function (){

            var self = this;

            var input = document.createElement('input');
            input.type = "file";
            input.onchange = function (event){
                var input = event.target;
    
                var reader = new FileReader();
                
                reader.onload = function(){
                    var text = reader.result;

                    self.$schema.loadSchema(JSON.parse(text));
                    window.location.reload();
                    
                };

                reader.readAsText(input.files[0]);
            };

            input.click();



        },

        downloadProject: function (){

            
            var filename = this.$schema.project.name + "." + this.$schema.project.version + ".json";
            var text = JSON.stringify(this.$schema.get()); 

            var a = document.createElement("a");
            // eslint-disable-next-line 
            var file = new Blob([text], {type: "application/json"});
            // eslint-disable-next-line 
            a.href = URL.createObjectURL(file);
            // eslint-disable-next-line 
		    a.download = filename;
            a.click();

        },

        openSettingsProject: function (){

            this.$popups.open({
                title: this.$ml.get('settings_project'),
                component: 'SettingsProjectPopup',
                data: {
                    project: this.$schema.project
                },

                buttons: [
                    {
                        text: this.$ml.get('save_action'),
                        type: 'success',
                        callback: () => {

                            var component = this.$popups.getComponent();
                            var name = component.name;
                            var error_counter = component.error_counter;

                            this.$schema.project.name = name;
                            this.$schema.project.vars.error_counter = error_counter;

                        }
                    },
                    {
                        text: this.$ml.get('cancel'),
                        type: 'cancel'
                    }
                ]
            })

        }

    }

}

</script>