<template>

    <div class="choose-project">

        <div class="title">
            <i class="icon icon-quack"></i>API Generator
        </div>

        <div class="project-list">
            <Dropdown class="dropdown" v-model="project" is_search="true" :search_placeholder="`${$ml.get('enter_project_name')}...`" :items="projects" :placeholder="`${$ml.get('choose_project')}...`" />
            <router-link :to="`/schema?project=${project}`" v-if="project != -1" class="submit button">Открыть проект</router-link>
            <button class="submit cancel" @click="uploadProject"><i class="icon fa fa-upload" ></i>Загрузить проект</button>
        </div>
        

    </div>

</template>

<script>

export default {

    data: function (){
        return {
            project: -1
        }
    },

    computed: {

        projects: function (){

            var projects = this.$store.state.projects.map(function (schema){

                return {
                    text: schema.project.name,
                    value: schema.project.id
                }

            });

            return projects;

        }

    },

    methods: {

        uploadProject: function (){

            var input = document.createElement('input');
            input.type = "file";
            input.onchange = (event) => {
                var input = event.target;
    
                var reader = new FileReader();
                
                reader.onload = () => {
                    var text = reader.result;

                    this.$schema.addProject(JSON.parse(text));

                    window.location.reload();
                    
                };

                reader.readAsText(input.files[0]);
            };

            input.click();

        }

    }

}

</script>

<style lang="scss" scoped>

.choose-project {

    display: flex;
    flex-direction: column;
    width: 350px;
    border: 1px solid #DCDCDC;
    border-radius: 5px;
    margin: 100px auto;
    background-color: #ffffff;
    box-shadow: 0px 1px 2px #DCDCDC;


    .title {
        display: flex;
        flex-direction: row;
        justify-content: center;
        padding: 30px;
        font-size: 25px;
        font-weight: 500;
        color: #5b95cd;

        .icon {
            margin-right: 7px;
            font-size: 30px;
        }
    }

    .project-list {

        display: flex;
        flex-direction: column;
        padding: 20px;
        padding-top: 0px;

        .submit {
            margin-top: 15px;
            font-size: 13px;

            .icon {
                margin-right: 7px;
            }
        }

    }

}

</style>