import Checkbox from "./components/Checkbox"
import Dropdown from "./components/Dropdown"
import RadioGroup from "./components/RadioGroup"
import Textarea from "./components/Textarea"
import ScrollView from "./components/ScrollView"
import ProgressBar from "./components/ProgressBar"
import DatePicker from "./components/DatePicker"
import Emoji from "./components/EmojiPicker/Emoji"
import EmojiPicker from "./components/EmojiPicker/EmojiPicker"
import Popups from "./components/Popups"



export default {

    install: function (Vue){

        Vue.component('Checkbox', Checkbox);
        Vue.component('Dropdown', Dropdown);
        Vue.component('RadioGroup', RadioGroup);
        Vue.component('Textarea', Textarea);
        Vue.component('ProgressBar', ProgressBar);
        Vue.component('ScrollView', ScrollView);
        Vue.component('DatePicker', DatePicker);
        Vue.component('EmojiPicker', EmojiPicker);
        Vue.component('Emoji', Emoji);
        Vue.component('Popups', Popups);

        Vue.directive('focus', {

            inserted: function (el) {
                el.focus();
            }

        });

        Vue.directive('click-outside', {

            bind: function (el, binding){

                document.body.addEventListener('click', function (event){

                    var el_path =  event.path || (event.composedPath && event.composedPath());
        
                    if(el_path.indexOf(el) == -1){
                        binding.value();
                    }
        
                });

            }

        });

        Vue.prototype.$popups = new Vue({

            created: function (){

                window.document.body.onkeyup = (event) => {

                    if(event.keyCode == 27){
                        this.close();
                    }

                };

            },

            data: function (){
                
                return {
                    component: null,
                    stack: [],
                }

            },

            computed: {

                current: function (){

                    return this.stack[this.stack.length - 1];

                }

            },

            methods: {

                open: function ({ title, html, component, data, buttons}){

                    this.stack.push({
                        title: title,
                        html: html,
                        component: component,
                        data: data,
                        buttons: buttons
                    });

                },

                close: function (){

                    this.stack.pop();

                },

                closeAll: function (){

                    this.stack = [];

                },

                getComponent: function (){

                    return this.component;

                },

                setCurrentComponent: function (comp){
                    this.component = comp;
                }

            }

        });

    }

};