<template>

    <div class="method-class">

        <div class="field">
            <div class="title">
                {{ $ml.get('method_name') }}
            </div>
            <input class="input" type="text" v-focus :placeholder="$ml.get('enter_method_name') + '...'" v-model="name" />
        </div>

        <div class="field">
            <div class="title">
                {{ $ml.get('description') }}
            </div>
            <div class="descriptions">
                <div class="description" v-for="(description, index) in descriptions" :key="description.lang">
                    <div class="subtitle">
                        <Dropdown v-model="description.lang" :items="languages" :placeholder="`${$ml.get('choose_language')}...`"/>
                        <div class="func">
                            <i class="icon fa fa-times" @click="removeDescription(index)"></i>
                        </div>
                    </div>
                    <Editor :height="'200px'" :options="markdown_options" mode="markdown" v-model="description.text"/>
                </div>
                <button class="cancel add_button" @click="addDescription"><i class="icon fa fa-plus"></i>{{ $ml.get('add_description') }}</button>
            </div>
        </div>

        <div class="field">
            <div class="title">
                {{ $ml.get('settings_access') }}
            </div>
            <Checkbox class="access_checkbox" v-model="is_admin">{{ $ml.get('need_admin_access') }}</Checkbox>
            <Checkbox class="access_checkbox" v-model="is_auth">{{ $ml.get('need_auth') }}</Checkbox>
            <Checkbox class="access_checkbox" v-model="is_db">{{ $ml.get('need_db_access') }}</Checkbox>
        </div>

        <div class="field">
            <div class="title">
                {{ $ml.get('error_list') }}
            </div>
            <div class="errors">
                <div class="list" v-if="errors.length > 0">
                    <div class="item" v-for="(error, index) in errors" :key="error">
                        <span class="code">#{{getErrorByCode(error).code}}</span> {{ getErrorByCode(error).title }}

                        <i class="fa fa-times remove_btn" @click="removeError(index)"></i>
                    </div>
                </div>
                <button class="add_button cancel" @click="addError"><i class="icon fa fa-plus"></i>{{ $ml.get('add_error') }}</button>
            </div>
        </div>

    </div>

</template>

<style lang="scss" scoped>


.method-class {
    
    display: flex;
    flex-direction: column;

    .field {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0px;
        }

        .title {
            font-size: 14px;
            margin-bottom: 10px;
            font-weight: 500;
        }

        .subtitle {
            display: flex;
            flex-direction: row;
            align-items: center;
            font-size: 13px;
            margin-bottom: 10px;

            .func {
                margin-left: auto;

                .icon {
                    color: #808080;
                    font-size: 14px;
                    cursor: pointer;                    

                    &:hover {
                        color: #5b96cd;
                    }

                }
            }

        }

        .input {
            font-size: 13px;
        }

        .descriptions {
            display: flex;
            flex-direction: column;
            width: auto;

            .description {
                display: flex;
                flex-direction: column;
                width: auto;
                margin-bottom: 10px;
            }

            .add_button {
                width: auto;

                .icon {
                    margin-right: 7px;
                }
            }
        }

        .textarea {
            width: auto;
            padding: 15px;
            border-radius: 3px;
            font-size: 13px;
        }

        .access_checkbox {
            font-size: 14px;
            margin-top: 5px;
            margin-bottom: 15px;
        }

        .errors {
            display: flex;
            flex-direction: column;

            .list {
                display: flex;
                flex-direction: column;
                border: 1px solid #ededed;
                border-radius: 5px;

                .item {

                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    padding: 15px;
                    border-bottom: 1px solid #ededed;
                    font-weight: 500;
                    font-size: 14px;

                    &:last-child {
                        border-bottom: none;
                    }

                    .code {
                        color: #808080;
                        margin-right: 7px;
                    }

                    .remove_btn {
                        margin-left: auto;
                        cursor: pointer;
                        color: #808080;

                        &:hover {
                            color: #5b96cd;
                        }
                    }

                }
            }

            .add_button {

                margin-top: 10px;

                .icon {
                    margin-right: 7px;
                }
            }
        }

    }

}

</style>

<script>

import Vue from 'vue'
import 'tui-editor/dist/tui-editor.css';
import 'tui-editor/dist/tui-editor-contents.css';
import 'codemirror/lib/codemirror.css';
import { Editor } from '@toast-ui/vue-editor'

export default {

    props: ['method_object'],

    components: {
        Editor
    },

    data: function (){

        return {
            name: "",
            descriptions: [],
            is_auth: false,
            is_db: false,
            is_admin: false,
            errors: [],

            languages: [
                {
                    text: this.$ml.get('ru'),
                    value: 'ru'
                },
                {
                    text: this.$ml.get('en'),
                    value: 'en'
                }
            ],

            markdown_options: {
                useCommandShortcut: true,
                useDefaultHTMLSanitizer: true,
                usageStatistics: true,
                hideModeSwitch: true,
                toolbarItems: [
                    'heading',
                    'bold',
                    'italic',
                    'strike',
                    'ul',
                    'ol'
                ]
            }
        }

    },


    created: function (){

        if(!this.$props.method_object){
            return;
        }

        this.name = this.$props.method_object.name;
        this.descriptions = JSON.parse(JSON.stringify(this.$props.method_object.descriptions));
        this.is_auth = this.$props.method_object.is_auth;
        this.is_db = this.$props.method_object.is_db;
        this.is_admin = this.$props.method_object.is_admin;
        this.errors = JSON.parse(JSON.stringify(this.$props.method_object.errors));

        this.is_creating = this.$props.is_creating;

    },

    methods: {

        getErrorByCode: function (code){

            if(!code) return 0;

            var schema = this.$store.state.projects.find((element) => {

                return (element.project.id == parseInt(this.$store.state.current_project));

            });

            var error = schema.errors.find((error) => {
                return error.code == code;
            });

            window.console.log(error);

            if(!error){
                return {
                    code: 0,
                    title: "Null error",
                    description: ""
                }
            }

            return error;

        },

        addError: function (){

            this.$popups.open({
                title: this.$ml.get('choose_error'),
                component: "ChooseErrorPopup",
                buttons: [
                    {
                        text: this.$ml.get('add_error'),
                        type: "success",
                        callback: () => {

                            var errorCode = this.$popups.getComponent().error;
                            
                            if(!errorCode || errorCode < 1){
                                return;
                            }

                            this.errors.push(errorCode);
                            
                        }
                    },
                    {
                        text: this.$ml.get('cancel'),
                        type: "cancel"
                    }
                ]
            })

        },

        removeError: function (index){

            Vue.delete(this.errors, index);

        },

        addDescription: function (){

            this.descriptions.push({
                lang: 'en',
                text: ''
            });
            
        },

        removeDescription: function (index){
            
            Vue.delete(this.descriptions, index);

        }

    }

}

</script>